/// <reference path="./AppTree.d.ts" />

import { Component, SyntheticEvent, Fragment, PureComponent, createRef } from 'react'

import Popup from 'semantic-ui-react/dist/commonjs/modules/Popup/Popup'
import Dropdown from 'semantic-ui-react/dist/commonjs/modules/Dropdown/Dropdown'

import PlanAuditoriaContext from './PlanAuditoriaContext'

import cx from 'classnames'
import { ProyectoPlanAuditoriaUtils } from './utils'
import {
  TreeItemFaseActions,
  TreeItemSubfaseActions,
  TreeItemPasoActions,
  DropdownItemAssignUser,
  DropdownItemDeletePasoVisita
} from './AppTreeItemActions'

declare var app: any

class TreeItemFase extends PureComponent<{ item: ItemFase }> {
  static contextType = PlanAuditoriaContext

  listRef = createRef<HTMLUListElement>()

  componentDidMount() {
    this.makeSortable()
  }

  toggle = (ev: SyntheticEvent) => {
    ev.preventDefault()

    const { tree, updateTree } = this.context
    const { item } = this.props

    tree[item.index].open = !item.open
    updateTree(tree)
  }

  makeSortable() {
    if (!app.vars.plan_auditoria.proyecto.isOpen || !user_has_role_permission('hoja_trabajo_edit')) {
      return false
    }

    const fase = this.props.item
    ;($(this.listRef.current) as any).sortable({
      handle: '.handle-dragdrop.subphase-handle',
      items: 'li.subphase',
      placeholder: 'sortable_placeholder',
      axis: 'y',
      revert: '100',
      forceHelperSize: false,
      containment: 'parent',
      helper: (ev: SyntheticEvent, item: any) => {
        const cloneItem = item.clone()
        $(cloneItem).find('ul.methodology-paso-list').remove()

        return cloneItem
      },
      start: function(ev: SyntheticEvent, ui: any) {
        ;($(this) as any).sortable('refreshPositions')
        $(ui.item).addClass('sortable_dragging')
      },
      stop: function(ev: SyntheticEvent, ui: any) {
        $(ui.item).removeClass('sortable_dragging')
      },
      update: (ev: SyntheticEvent, ui: any) => {
        var items = ($(this.listRef.current) as any).sortable('toArray')

        $('.methodology-item.subphase.sortable-enabled').removeClass('sortable-enabled')

        items = items.map(function(item: string) {
          return item.substr(8)
        })

        $.blockUI(create_blockUI_loading_message('Guardando los cambios'))
        $.post(url_for('proyecto/' + app.vars.idproyecto + '/master_data/rubro/sort'), { sort: items }).done((res) => {
          if (res.success == 1) {
            const positions: { [s: string]: number } = {}
            items.each((v: string, k: number) => {
              positions[v] = k
            })

            const { tree, updateTree } = this.context

            tree[fase.index].items = _.map(tree[fase.index].items, (rubro: ItemSubfase) => {
              rubro.position = '' + (positions[rubro.id] + 1)
              rubro.sortIndex = positions[rubro.id]
              return rubro
            })

            updateTree(tree)
            $.unblockUI()
          }
        })
      }
    })
  }

  render() {
    const { item } = this.props

    const editable = app.vars.plan_auditoria.proyecto.isOpen

    const itemClasses = cx('methodology-item phase', item.items.length > 0 ? 'has-rubros' : 'no-rubros')

    return (
      <li className={itemClasses} id={`fase${item.id}`}>
        <div className='methodology-item-head'>
          <span className='handle handle-dragdrop phase-handle' />
          <a className='phase-name' onClick={this.toggle} href='#'>
            <span className='fase-iteration'>{_.str.lpad(item.sortIndex + 1, 2, '0')}</span>-{_.str.trim(item.descripcion.toUpperCase())}
          </a>
          {editable && <TreeItemFaseActions item={item} />}
          <div className='clearfix' />
        </div>
        <ul className='methodology methodology-rubro-list' ref={this.listRef}>
          {item.open &&
            _.map(_.sortBy(item.items, (subitem) => parseInt(subitem.position)), (subfase: ItemSubfase) => {
              subfase.parent = item
              return <TreeItemSubfase key={subfase.id} item={subfase} />
            })}
        </ul>
      </li>
    )
  }
}

class TreeItemSubfase extends PureComponent<{ item: ItemSubfase }> {
  static fetchingSumarias = false
  static contextType = PlanAuditoriaContext

  fetchingRiesgos = false
  listRef = createRef<HTMLUListElement>()

  static getIndexChar(index: number) {
    let prefix = ''
    if (index > 25) {
      prefix = String.fromCharCode(97 + (Math.floor(index / 25) - 1))
    }
    return prefix + String.fromCharCode(97 + index % 26)
  }

  toggle = (ev: SyntheticEvent) => {
    ev.preventDefault()

    const { item } = this.props
    const { tree, updateTree } = this.context

    tree[item.parent.index].items[item.index].open = !item.open
    updateTree(tree)
  }

  componentDidMount() {
    this.loadSumarias()
    this.loadRiesgos()
    this.makeSortable()
  }

  componentDidUpdate() {
    this.loadSumarias()
    this.checkSortable()
  }

  loadSumarias() {
    if (TreeItemSubfase.fetchingSumarias) {
      return
    }

    const { item } = this.props
    const { tree, updateTree } = this.context
    const hasFilters = _.size(this.context.filters) > 0

    if (item.sumarias === null && item.parent.id == app.vars.plan_auditoria.settings.ejecucion_fase_id && !hasFilters) {
      TreeItemSubfase.fetchingSumarias = true

      $.getJSON(url_for(`proyecto/${app.vars.idproyecto}/master_data_tree`), {
        export_options: { type: 'sumarias', fase_id: item.parent.id }
      }).done((res) => {
        _.each(res.response, (totales, rubro_id: string) => {
          const rubroIndex = _.findIndex(tree[item.parent.index].items, { id: rubro_id })
          tree[item.parent.index].items[rubroIndex].sumarias = totales
        })
        updateTree(tree)
      })
    }
  }

  loadRiesgos() {
    if (this.fetchingRiesgos) {
      return
    }

    this.fetchingRiesgos = true

    const { item } = this.props
    const { tree, updateTree } = this.context

    if (tree[item.parent.index].items[item.index].riesgosLoaded) {
      return
    }

    $.getJSON(url_for(`proyecto/${app.vars.idproyecto}/master_data_tree`), {
      export_options: { type: 'riesgos', rubro_id: item.id }
    }).done((res) => {
      _.each(res.response, (riesgos, paso_id: string) => {
        const pasoIndex = _.findIndex(tree[item.parent.index].items[item.index].items, { id: paso_id })
        tree[item.parent.index].items[item.index].items[pasoIndex].riesgos = riesgos
      })

      tree[item.parent.index].items[item.index].riesgosLoaded = true

      updateTree(tree)
    })
  }

  makeSortable() {
    if (!app.vars.plan_auditoria.proyecto.isOpen || !user_has_role_permission('hoja_trabajo_edit')) {
      return false
    }

    const rubro = this.props.item

    const { filters } = this.context
    const $sortableList: any = $(this.listRef.current)
    $sortableList.sortable({
      handle: '.handle-dragdrop.paso-handle, .sortable-enabled ',
      items: 'li.paso.paso-sortable',
      placeholder: 'sortable_placeholder',
      axis: 'y',
      revert: '100',
      helper: 'clone',
      disabled: _.size(filters) > 0,
      containment: 'parent',
      start: (ev: SyntheticEvent, ui: any) => {
        $(ui.item).addClass('sortable_dragging')
      },
      stop: function(ev: SyntheticEvent, ui: any) {
        $(ui.item).removeClass('sortable_dragging')
      },
      update: (ev: SyntheticEvent, ui: any) => {
        var items = $sortableList.sortable('toArray')

        $('.methodology-item.paso.sortable-enabled').removeClass('sortable-enabled')

        items = items.map(function(item: string) {
          return item.substr(5)
        })

        $.blockUI(create_blockUI_loading_message('Guardando los cambios'))
        $.post(url_for('proyecto/' + app.vars.idproyecto + '/master_data/paso/sort'), { sort: items }).done((res) => {
          if (res.success == 1) {
            const positions: { [s: string]: number } = {}
            items.each((v: string, k: number) => {
              positions[v] = k
            })

            const { tree, updateTree } = this.context

            tree[rubro.parent.index].items[rubro.index].items = _.map(
              tree[rubro.parent.index].items[rubro.index].items,
              (paso: ItemPaso) => {
                paso.position = '' + (positions[paso.id] + 1)
                paso.sortIndex = positions[paso.id]
                return paso
              }
            )

            updateTree(_.clone(tree, true))
            $.unblockUI()
          }
        })
      }
    })
  }

  checkSortable() {
    if (!app.vars.plan_auditoria.proyecto.isOpen || !user_has_role_permission('hoja_trabajo_edit')) {
      return false
    }

    const { filters } = this.context
    if (_.size(filters)) {
      ;($(this.listRef.current) as any).sortable('option', 'disable', true)
    } else {
      ;($(this.listRef.current) as any).sortable('option', 'disable', false)
    }
  }

  matchesPasoToFilters(item: ItemPaso, filters: any) {
    if (_.size(filters) == 0) {
      return true
    }

    return _.some(item.paso_visitas, (paso_visita) => {
      if (filters.visita && paso_visita.id_proyecto_visita == filters.visita) {
        return true
      }

      if (filters.status && paso_visita.id_proyecto_paso_estado == filters.status) {
        return true
      }

      if (filters.asignado && paso_visita.id_usuario_asignado == filters.asignado) {
        return true
      }

      return false
    })
  }

  render() {
    const { item } = this.props
    const hasFilters = _.size(this.context.filters) > 0
    const editable = app.vars.plan_auditoria.proyecto.isOpen

    const pasosElements = _.map(_.sortBy(item.items, (item) => parseInt(item.position)), (paso: ItemPaso) => {
      paso.parent = item

      if (this.matchesPasoToFilters(paso, this.context.filters)) {
        return <TreeItemPaso key={paso.id} item={paso} />
      }

      return null
    })

    const itemClasses = cx(
      'methodology-item subphase',
      _.filter(pasosElements).length == 0 ? 'no-pasos' : 'has-pasos',
      hasFilters ? 'has-filters' : ''
    )

    return (
      <li className={itemClasses} id={`subphase${item.id}`}>
        <div className='methodology-item-head'>
          <span className='handle handle-dragdrop subphase-handle' />
          <a className='phase-name' onClick={this.toggle} href='#'>
            <span className='fase-iteration'>{TreeItemSubfase.getIndexChar(item.sortIndex).toUpperCase()}</span>-{_.str.trim(item.descripcion)}
          </a>
          {editable && <TreeItemSubfaseActions item={item} />}
          <div className='clearfix' />
        </div>
        <ul className='methodology methodology-paso-list' ref={this.listRef}>
          {item.open && (
            <Fragment>
              {app.vars.plan_auditoria.settings.ejecucion_fase_id == item.parent.id &&
              !hasFilters && <TreeItemPasoSubsumaria rubro={item} />}
              {pasosElements}
            </Fragment>
          )}
        </ul>
      </li>
    )
  }
}

class TreeItemPaso extends PureComponent<{ item: ItemPaso }> {
  static contextType = PlanAuditoriaContext

  countRiesgos() {
    const { item } = this.props

    if (item.riesgos === null) {
      return <div className='ui mini active inline loader' />
    } else if (_.size(item.riesgos) === 0) {
      return 0
    } else {
      const riesgosChunk = _.take(item.riesgos, 30)
      const riesgosContent = (
        <div>
          {_.map(riesgosChunk, (riesgo) => riesgo.code).join(', ')}

          {_.size(item.riesgos) > _.size(riesgosChunk) && ` y ${_.size(item.riesgos) - _.size(riesgosChunk)} ms...`}
        </div>
      )

      return (
        <Popup header='Riesgos' content={riesgosContent} size='mini' trigger={<span>{_.size(item.riesgos)}</span>} />
      )
    }
  }

  render() {
    const { item } = this.props

    const pasoLiClassnames = cx(
      'methodology-item paso paso-sortable',
      `paso-code-${item.code}`,
      `paso-tipo-${(item.tipo || 'default').toLowerCase()}`
    )

    const editable = app.vars.plan_auditoria.proyecto.isOpen
    const hallazgosCount = app.vars.plan_auditoria.paso_hallazgos[item.id] || 0
    const riesgosCount = this.countRiesgos()

    return (
      <li className={pasoLiClassnames} id={`paso-${item.id}`}>
        <div className='methodology-item-head'>
          <span className='handle handle-dragdrop paso-handle' />
          <a href={url_for(`proyecto//${app.vars.idproyecto}/hoja-trabajo/paso/${item.id}`)} className='paso-name'>
            <span className='paso-iteration'>{_.str.lpad(item.sortIndex + 1, 3, '0')}</span>-{item.descripcion}
          </a>

          {editable && <TreeItemPasoActions item={item} />}

          <div className='pull-right sf_admin_td_actions valign'>
            <div className='section-a'>
              {item.tipo == 'PERIODO' ? (
                <TreeItemPasoPeriodoVisitaList paso={item} />
              ) : (
                <TreeItemPasoDefaultVisitaList paso={item} />
              )}
            </div>

            <div className='divider-vertical' />
            <div className='section-a'>
              <div className='header'>
                <strong className='title'>Riesgos</strong>
              </div>

              <span>{riesgosCount}</span>
            </div>

            <div className='section-a'>
              <div className='header'>
                <strong className='title'>Hallazgos</strong>
              </div>
              <span>{hallazgosCount}</span>
            </div>

            <div className='divider-vertical' />

            <div
              className={item.aserciones.length > 0 ? 'section-a' : ''}
              style={{ display: 'inline-block', marginRight: 10, marginTop: -5, minWidth: 250, width: 250 }}
            >
              {item.aserciones.length > 0 ? (
                <Fragment>
                  <div className='header'>
                    <strong className='title'>Aserciones</strong>
                  </div>
                  <ul className='unstyled'>
                    {_.map(item.aserciones, (asercion) => {
                      const objetivo: ProyectoObjetivo = _.find(app.vars.plan_auditoria.objetivos, {
                        id: asercion.id_master_data_objetivo
                      })
                      return (
                        <li key={asercion.id_master_data_objetivo} className='label label-info objetivo-sigla'>
                          <Popup
                            basic
                            size='mini'
                            content={objetivo.description}
                            header={`Asercin: ${objetivo.name}`}
                            trigger={<span>{objetivo.code}</span>}
                          />
                        </li>
                      )
                    })}
                  </ul>
                </Fragment>
              ) : (
                <span>&nbsp;</span>
              )}
            </div>
          </div>
          <div className='clearfix' />
        </div>
      </li>
    )
  }
}

class TreeItemPasoSubsumaria extends PureComponent<{ rubro: ItemSubfase }> {
  static contextType = PlanAuditoriaContext
  render() {
    const { rubro } = this.props
    const { visitas } = this.context

    return (
      <li className='methodology-item paso sumaria'>
        <div className='methodology-item-head'>
          <span className='handle' />
          <a
            href={url_for(`proyecto/${app.vars.idproyecto}/master_data/rubro/${rubro.id}/sumarias`)}
            className='paso-name'
          >
            000-Subsumaria
          </a>
          <div className='sf_admin_td_actions'>
            <div className='section-a'>
              {_.map(visitas, (visita: ProyectoVisita) => {
                return <TreeItemPasoSubsumariaVisita key={`${rubro.id}-${visita.id}`} rubro={rubro} visita={visita} />
              })}
            </div>
          </div>
          <div className='clearfix' />
        </div>
      </li>
    )
  }
}

class TreeItemPasoSubsumariaVisita extends PureComponent<{ rubro: ItemSubfase; visita: ProyectoVisita }> {
  static contextType = PlanAuditoriaContext

  getTotal() {
    const { rubro, visita } = this.props
    const sumarias = this.context.tree[rubro.parent.index].items[rubro.index].sumarias

    if (sumarias === null) {
      return <div className='ui mini active inline loader' />
    }
    if (typeof sumarias[visita.id] === 'undefined' || sumarias[visita.id] === null) {
      return '-'
    }
    return accounting.formatMoney(sumarias[visita.id])
  }

  render() {
    const { visita } = this.props

    return (
      <div className='visita'>
        <a className='title pasovisita-name'>{shortVisitaAlias(visita.name)}</a>
        <div>
          <div className='subsumaria-total'>{this.getTotal()}</div>
        </div>
      </div>
    )
  }
}

class TreeItemPasoPeriodoVisitaList extends PureComponent<{ paso: ItemPaso }> {
  static contextType = PlanAuditoriaContext

  onContextMenu = (ev: React.SyntheticEvent) => {
    ev.preventDefault()
    $(ev.currentTarget).find('i.dropdown.icon').click()
  }

  getDropdownItems(paso_visita: any) {
    const { paso } = this.props
    const linkurl = url_for(
      `proyecto/${app.vars.idproyecto}/hoja-trabajo/paso/${paso.id}?id_visita=${paso_visita.id_proyecto_visita}`
    )

    const is_editable = app.vars.plan_auditoria.proyecto.isOpen

    const dropdownitems = (
      <Fragment>
        <Dropdown.Item key='link'>
          <a href={linkurl}>Ir a Hoja de Trabajo</a>
        </Dropdown.Item>
        {is_editable &&
        user_has_role_permission([ 'paso_asignar_usuario' ]) && (
          <DropdownItemAssignUser key='assignuser' paso_visita={paso_visita} paso={paso} />
        )}
        {is_editable &&
        user_has_role_permission('hoja_trabajo_edit') && (
          <DropdownItemDeletePasoVisita key='delete' paso_visita={paso_visita} paso={paso} />
        )}
      </Fragment>
    )

    return dropdownitems
  }

  render() {
    const { paso } = this.props
    const paso_visitas = _.sortBy(paso.paso_visitas, (item) => parseInt(item.position))

    return (
      <Fragment>
        {_.map(paso_visitas, (paso_visita, index) => {
          const paso_estado: ProyectoPasoEstado = _.findWhere(app.vars.plan_auditoria.paso_estados, {
            id_proyecto_paso_estado: paso_visita.id_proyecto_paso_estado
          })
          const user = ProyectoPlanAuditoriaUtils.getUserById(paso_visita.id_usuario_asignado)
          const linkUrl = url_for(
            `proyecto/${app.vars.idproyecto}/hoja-trabajo/paso/${paso.id}?id_visita=${paso_visita.id}`
          )

          return [
            <div className='visita' key={`visita-${paso_visita.id}`}>
              <div className='header' onContextMenu={this.onContextMenu}>
                <a className='title pasovisita-name has-contextmenu' href={linkUrl}>
                  {shortVisitaAlias(paso_visita.visita_alias)}
                </a>
                <Dropdown compact inline icon={{ name: 'dropdown', size: 'small' }} className='contextmenu'>
                  <Dropdown.Menu>{this.getDropdownItems(paso_visita)}</Dropdown.Menu>
                </Dropdown>
              </div>
              <a className='title pasovisita-name has-contextmenu' key={paso_visita.id} href={linkUrl} />
              <div>
                <div title={user ? `Asignado a: ${user.name}` : 'Sin Asignar'} style={{ display: 'inline-block' }}>
                  {user ? user.sigla : ''}&nbsp;
                </div>
                <div className='label visita-status' style={{ backgroundColor: paso_estado.color }}>
                  <span title={`Estado: ${paso_estado.name}`}>{wordInitials(paso_estado.name)}</span>
                </div>
              </div>
            </div>,
            index == paso_visitas.length - 1 ? null : (
              <div className='divider-vertical' key={`divider-${paso_visita.id}`} />
            )
          ]
        })}
      </Fragment>
    )
  }
}

class TreeItemPasoDefaultVisitaList extends PureComponent<{ paso: ItemPaso }> {
  static contextType = PlanAuditoriaContext

  getVisitaById(id_visita: string): ProyectoVisita {
    const { visitas } = this.context

    return _.findWhere(visitas, { id: id_visita })
  }

  getPasoVisitas() {
    const { paso } = this.props

    let paso_visitas = _.map(paso.paso_visitas, (item) => {
      const visita = this.getVisitaById(item.id_proyecto_visita)
      if (!visita) {
        return null
      }
      item.position = +visita.position
      return item
    })

    paso_visitas = _.filter(paso_visitas)

    return _.sortBy(paso_visitas, 'position')
  }

  getDropdownItems(paso_visita: any) {
    const { paso } = this.props

    const is_editable = app.vars.plan_auditoria.proyecto.isOpen

    let dropdownitems

    const linkurl = url_for(
      `proyecto/${app.vars.idproyecto}/hoja-trabajo/paso/${paso.id}?id_visita=${paso_visita.id_proyecto_visita}`
    )

    if (paso.tipo == 'MATERIALIDAD') {
      dropdownitems = (
        <Fragment>
          <Dropdown.Item>
            <a href={linkurl}>Ir a Hoja de Trabajo</a>
          </Dropdown.Item>
          {is_editable &&
          user_has_role_permission('hoja_trabajo_edit') && (
            <DropdownItemAssignUser paso_visita={paso_visita} paso={paso} />
          )}
        </Fragment>
      )
    } else {
      dropdownitems = (
        <Fragment>
          <Dropdown.Item>
            <a href={linkurl}>Ir a Hoja de Trabajo</a>
          </Dropdown.Item>
          {is_editable &&
          user_has_role_permission('visita_edit') && (
            <Dropdown.Item onClick={() => this.onClickRename(paso_visita)}>Cambiar nombre</Dropdown.Item>
          )}
          {is_editable &&
          user_has_role_permission('hoja_trabajo_edit') && (
            <DropdownItemAssignUser paso_visita={paso_visita} paso={paso} />
          )}
          {is_editable &&
          user_has_role_permission('hoja_trabajo_edit') && (
            <DropdownItemDeletePasoVisita paso_visita={paso_visita} paso={paso} />
          )}
        </Fragment>
      )
    }

    return dropdownitems
  }

  onClickRename = (paso_visita: any) => {
    const { visitas, updateVisitas } = this.context

    const visita: ProyectoVisita = _.find(visitas, { id_proyecto_visita: paso_visita.id_proyecto_visita })

    const prompt = bootbox.prompt(
      {
        header: `Renombrar Visita - ${visita.name}`,
        content: '<div>Escriba el nuevo nombre que desea asignarle a la visita seleccionada.</div>'
      },
      'Cancelar',
      'Actualizar',
      (r: string) => {
        if (!r || _.str.trim(r) == '') {
          return
        }

        $.blockUI(create_blockUI_loading_message('Actualizando Visita...'))
        $.post(url_for(`proyecto/${app.vars.idproyecto}/master_data/visita/${visita.id}`), { visita_name: r })
          .done((res) => {
            if (!res.hasErrors) {
              visita.name = res.response.record.name

              updateVisitas(visitas)
              prompt.modal('hide')

              $.unblockUI()
            } else {
              $.unblockUI()
              bootbox.alert(
                `<p>El siguiente ocurri al intentar actualizar la visita:</p>
                <div class="alert alert-error alert-block _a-center">${res.message}</div>`
              )
            }
          })
          .fail(() => {
            $.pnotify({ type: 'error', text: 'Un error inesperado ocurri al intentar actualizar la visita.' })
            $.unblockUI()
          })

        return false
      },
      visita.name
    )
    $('.bootbox.modal').addClass('bootbox-prompt modal-ab')
  }

  onContextMenu = (ev: React.SyntheticEvent) => {
    ev.preventDefault()
    $(ev.currentTarget).find('i.dropdown.icon').click()
  }

  render() {
    const { paso } = this.props
    const paso_visitas = this.getPasoVisitas()

    return (
      <Fragment>
        {_.map(paso_visitas, (paso_visita, index) => {
          const paso_estado: ProyectoPasoEstado = _.findWhere(app.vars.plan_auditoria.paso_estados, {
            id_proyecto_paso_estado: paso_visita.id_proyecto_paso_estado
          })
          const user = ProyectoPlanAuditoriaUtils.getUserById(paso_visita.id_usuario_asignado)
          const linkUrl = url_for(
            `proyecto/${app.vars.idproyecto}/hoja-trabajo/paso/${paso.id}?id_visita=${paso_visita.id_proyecto_visita}`
          )

          return [
            <div className='visita' key={`visita-${paso_visita.id}`}>
              <div className='header' onContextMenu={this.onContextMenu}>
                <a className='title pasovisita-name has-contextmenu' href={linkUrl}>
                  {shortVisitaAlias(this.getVisitaById(paso_visita.id_proyecto_visita).name)}
                </a>
                <Dropdown compact inline icon={{ name: 'dropdown', size: 'small' }} className='contextmenu'>
                  <Dropdown.Menu>{this.getDropdownItems(paso_visita)}</Dropdown.Menu>
                </Dropdown>
              </div>

              <div>
                <div
                  title={user ? `Asignado a: ${user.name}` : 'Sin Asignar'}
                  style={{ display: 'inline-block', width: 30, fontSize: 10 }}
                >
                  {user ? user.sigla : ''}&nbsp;
                </div>
                <div className='label visita-status' style={{ backgroundColor: paso_estado.color }}>
                  {paso_visitas.length >= 6 ? (
                    <span title={`Estado: ${paso_estado.name}`}>{wordInitials(paso_estado.name)}</span>
                  ) : (
                    paso_estado.name
                  )}
                </div>
              </div>
            </div>,
            index == paso_visitas.length - 1 ? null : (
              <div className='divider-vertical' key={`divider-${paso_visita.id}`} />
            )
          ]
        })}
      </Fragment>
    )
  }
}

const parseTree = (input: any, hasFilters: boolean = false): ItemFase[] => {
  return _.map(input, (fase: any, index: number) => {
    return {
      id: fase.id,
      code: fase.code,
      descripcion: fase.descripcion,
      position: fase.position,
      index,
      sortIndex: index,
      open: hasFilters,
      items: _.map(fase.Subfases, (subfase: any, index: number) => {
        return {
          id: subfase.id,
          code: subfase.code,
          descripcion: subfase.descripcion,
          position: subfase.position,
          index,
          sortIndex: index,
          open: hasFilters,
          sumarias: null,
          riesgosLoaded: false,
          items: _.map(subfase.MasterDataPaso, (paso: any, index: number) => {
            return {
              id: paso.id,
              code: paso.code,
              descripcion: paso.descripcion,
              position: paso.position,
              tipo: paso.paso_tipo,
              paso_visitas: paso.MasterDataPasoHasProyectoVisita,
              aserciones: paso.MasterDataPasoHasObjetivo,
              riesgos: null,
              index,
              sortIndex: index
            }
          })
        }
      })
    }
  })
}

const shortVisitaAlias = (visitaName: string) => {
  const replace_pairs = {
    Enero: 'Ene',
    Febrero: 'Feb',
    Marzo: 'Mar',
    Abril: 'Abr',
    Mayo: 'May',
    Junio: 'Jun',
    Julio: 'Jul',
    Agosto: 'Ago',
    Septiembre: 'Sept',
    Octubre: 'Oct',
    Noviembre: 'Nov',
    Diciembre: 'Dic'
  }

  _.each(replace_pairs, (replace, from) => {
    visitaName = visitaName.replace(new RegExp(from, 'ig'), replace)
  })

  return visitaName
}

const wordInitials = (str: string) => str.match(/\b\w/g).join('').toUpperCase()

const openDefault = (tree: any, defaultOpen: { element: 'fase' | 'rubro' | 'paso'; value: string }) => {
  let found = false

  switch (defaultOpen.element) {
    case 'fase':
      const index = _.findIndex(tree, { id: defaultOpen.value })
      tree[index].open = true
      break
    case 'rubro':
      found = false
      _.each(tree, (fase) => {
        _.each(fase.items, (subfase) => {
          if (subfase.id == defaultOpen.value) {
            found = true
            fase.open = true
            subfase.open = true

            return false
          }
        })

        if (found) {
          return false
        }
      })
      break
    case 'paso':
      found = false
      _.each(tree, (fase) => {
        _.each(fase.items, (subfase) => {
          _.each(subfase.items, (paso) => {
            if (paso.id == defaultOpen.value) {
              found = true
              fase.open = true
              subfase.open = true

              return false
            }
          })

          if (found) {
            return false
          }
        })

        if (found) {
          return false
        }
      })
      break
  }
}

export default class AppTree extends Component {
  static contextType = PlanAuditoriaContext
  listRef = createRef<HTMLUListElement>()

  componentDidMount() {
    let { tree, updateTree, filters } = this.context

    if (tree === null) {
      $.getJSON(url_for(`proyecto/${app.vars.idproyecto}/master_data_tree`), {
        export_options: { type: 'base' }
      }).done((res) => {
        app.vars.plan_auditoria.fases = res.response
        const hasFilters = _.size(filters) > 0
        tree = parseTree(res.response, hasFilters)
        if (!hasFilters) {
          openDefault(tree, app.vars.plan_auditoria.settings.default_open)
        }
        updateTree(tree)
      })
    }

    this.makeSortable()
  }

  makeSortable() {
    if (!app.vars.plan_auditoria.proyecto.isOpen || !user_has_role_permission('hoja_trabajo_edit')) {
      return false
    }

    ;($(this.listRef.current) as any).sortable({
      handle: '.handle-dragdrop.phase-handle',
      items: 'li.phase',
      placeholder: 'sortable_placeholder',
      axis: 'y',
      revert: '100',
      forceHelperSize: false,
      containment: 'parent',
      helper: (ev: SyntheticEvent, item: any) => {
        const cloneItem = item.clone()
        $(cloneItem).find('ul.methodology-rubro-list').remove()

        return cloneItem
      },
      start: function(ev: SyntheticEvent, ui: any) {
        ;($(this) as any).sortable('refreshPositions')
        $(ui.item).addClass('sortable_dragging')
      },
      stop: function(ev: SyntheticEvent, ui: any) {
        $(ui.item).removeClass('sortable_dragging')
      },
      update: (ev: SyntheticEvent, ui: any) => {
        let items = ($(this.listRef.current) as any).sortable('toArray')

        items = items.map(function(item: string) {
          return item.substr(4)
        })

        $.blockUI(create_blockUI_loading_message('Guardando los cambios'))
        $.post(url_for('proyecto/' + app.vars.idproyecto + '/master_data/rubro/sort'), { sort: items }).done((res) => {
          if (res.success == 1) {
            const positions: { [s: string]: number } = {}
            items.each((v: string, k: number) => {
              positions[v] = k
            })

            const { tree, updateTree } = this.context

            const newtree = _.map(tree, (fase: ItemSubfase) => {
              fase.position = '' + (positions[fase.id] + 1)
              fase.sortIndex = positions[fase.id]
              return fase
            })

            updateTree(newtree)
            $.unblockUI()
          }
        })
      }
    })
  }

  render() {
    const { tree } = this.context

    return (
      <Fragment>
        {tree === null && (
          <div className='ui basic segment' style={{ minHeight: 200 }}>
            <div className='ui active inverted dimmer'>
              <div className='ui text loader elastic olive'>Cargando Plan de Auditora</div>
            </div>
            <p />
          </div>
        )}
        <ul
          className='methodology methodology-all methodology-sortable nav nav-stacked sf_admin_list_records'
          id='methodology-accordion'
          ref={this.listRef}
        >
          {tree !== null &&
            _.map(_.sortBy(tree, (subitem: ItemFase) => parseInt(subitem.position)), (fase: ItemFase) => {
              return <TreeItemFase key={fase.id} item={fase} />
            })}
        </ul>
      </Fragment>
    )
  }
}
