import { Component, useState, SyntheticEvent, createContext, useContext, Fragment } from 'react'

import Modal from 'semantic-ui-react/dist/commonjs/modules/Modal/Modal'
import Dropdown from 'semantic-ui-react/dist/commonjs/modules/Dropdown/Dropdown'
import Step from 'semantic-ui-react/dist/commonjs/elements/Step/Step'
import Popup from 'semantic-ui-react/dist/commonjs/modules/Popup/Popup'

import cx from 'classnames'

import { ProyectoPlanAuditoriaUtils } from './utils'

declare var app: any

type HojaTrabajoTreeItem = {
  id: string
  descripcion: string
  type: string
  selected: boolean
  items: HojaTrabajoTreeItem[] | HojaTrabajoTreeItemVisita[]
  parent: HojaTrabajoTreeItem
}

type HojaTrabajoTreeItemVisita = {
  id: string
  type: string
  alias: string
  id_visita: string
  user_id: string
  selected: boolean
  parent: HojaTrabajoTreeItem
}

const MasterDataTreeContext = createContext<MasterDataTreeContextType>({
  tree: [],
  updateTree: null,
  filters: { ignore_assigned: false },
  updateFilter: null
})

const countPendingForAssign = (item: HojaTrabajoTreeItem) => {
  let assigned = 0,
    total = 0,
    checked = 0

  if (item.type == 'paso') {
    total = _.size(item.items as HojaTrabajoTreeItemVisita[])
    assigned = _.size(_.reject(item.items as HojaTrabajoTreeItemVisita[], { user_id: null }))
    checked = _.size(_.filter(item.items as HojaTrabajoTreeItemVisita[], { selected: true }))
  } else {
    _.each(item.items, (subitem) => {
      const { assigned: _assigned, checked: _checked, total: _total } = countPendingForAssign(subitem)

      assigned += _assigned
      checked += _checked
      total += _total
    })
  }
  return { assigned, checked, total }
}

const parseTreeToItems = (tree: any[]) => {
  const retval = _.map(tree, (fase): HojaTrabajoTreeItem => {
    return {
      id: fase.id,
      descripcion: fase.descripcion,
      type: 'phase',
      selected: false,
      parent: null,
      items: _.map(fase.Subfases, (subfase: any): HojaTrabajoTreeItem => {
        return {
          id: subfase.id,
          descripcion: subfase.descripcion,
          type: 'subphase',
          selected: false,
          parent: null,
          items: _.map(subfase.MasterDataPaso, (paso: any): HojaTrabajoTreeItem => {
            return {
              id: paso.id,
              descripcion: paso.descripcion,
              type: 'paso',
              selected: false,
              parent: null,
              items: _.map(paso.MasterDataPasoHasProyectoVisita, (paso_visita: any): HojaTrabajoTreeItemVisita => {
                return {
                  id: paso_visita.id,
                  type: 'pasovisita',
                  alias: paso_visita.visita_alias,
                  id_visita: paso_visita.id_proyecto_visita,
                  user_id: paso_visita.id_usuario_asignado,
                  parent: null,
                  selected: false
                }
              })
            }
          })
        }
      })
    }
  })

  return retval
}

const TreeItemVisita = ({ item }: { item: HojaTrabajoTreeItemVisita }) => {
  const visita = _.size(item.alias) ? { name: item.alias } : ProyectoPlanAuditoriaUtils.getVisitaById(item.id_visita)
  const user = ProyectoPlanAuditoriaUtils.getUserById(item.user_id)

  const { updateTree, filters } = useContext(MasterDataTreeContext)

  const onToggleCheckbox = () => {
    item.selected = !item.selected
    updateTree(item)
  }

  return (
    <li key={item.id} className={cx('methodology-item visita', user ? 'assigned' : 'unassigned')}>
      <label htmlFor={`massassign-paso-visita-${item.id}`}>
        <input
          type='checkbox'
          id={`massassign-paso-visita-${item.id}`}
          checked={item.selected}
          className='checkbox'
          disabled={filters.ignore_assigned && !!item.user_id}
          onChange={onToggleCheckbox}
        />
        <span className='item-name'>{visita ? visita.name : '-'}</span>
        <span className={cx('user-assigned', user ? 'label label-success' : '')}>{user ? user.sigla : ''}</span>
      </label>
    </li>
  )
}

const TreeItemFase = ({ item }: { item: HojaTrabajoTreeItem }) => {
  const [ collapsed, setCollapsed ] = useState(true)

  const onClick = (ev: SyntheticEvent) => {
    ev.preventDefault()
    setCollapsed(!collapsed)
  }

  const { updateTree, filters } = useContext(MasterDataTreeContext)

  const onToggleCheckbox = () => {
    item.selected = !item.selected
    updateTree(item)

    if (filters.ignore_assigned && hasUncheckableItems(item)) {
      $.pnotify({
        title: 'Advertencia',
        text: 'Este elemento contiene pasos/visitas que no pueden ser seleccionados debido que ya fueron asignados. ',
        delay: 1000 * 2
      })
    }
  }

  const { assigned, checked, total } = countPendingForAssign(item)
  const hasPending = total > 0 && assigned < total

  return (
    <li key={item.id} className={cx('methodology-item', item.type)}>
      <div className='methodology-item-head'>
        <a href='#' onClick={onClick}>
          <span className='item-name'>{item.descripcion}</span>
        </a>
        <input
          type='checkbox'
          checked={item.selected}
          title={'Seleccionar todos'}
          className='checkbox'
          disabled={_.size(item.items as any[]) == 0}
          onChange={onToggleCheckbox}
        />

        {checked > 0 && (
          <strong className='checked-counter pull-right' title='# Hojas de Trabajo seleccionadas'>
            ({checked})
          </strong>
        )}

        <span
          className={cx('badge badge-counter', hasPending ? 'badge-important' : 'badge-success')}
          title='# Hojas de trabajo con usuario responsable asignado / # Hojas de trabajo en total'
        >
          {assigned} / {total}
        </span>
      </div>
      <ul className={cx('methodology collapse', collapsed ? 'hide' : 'in')}>
        {_.map(item.items as any[], (subitem) => {
          subitem.parent = item
          return subitem.type == 'pasovisita' ? (
            <TreeItemVisita key={subitem.id} item={subitem} />
          ) : (
            <TreeItemFase key={subitem.id} item={subitem} />
          )
        })}
      </ul>
    </li>
  )
}

const HojaTrabajoTree = () => {
  return (
    <MasterDataTreeContext.Consumer>
      {({ tree }) => (
        <ul className='methodology checkbox-list methodology-all nav nav-stacked'>
          {_.map(tree, (fase: HojaTrabajoTreeItem) => {
            return <TreeItemFase key={`tree-item-${fase.type}-${fase.id}`} item={fase} />
          })}
        </ul>
      )}
    </MasterDataTreeContext.Consumer>
  )
}

const Filters = () => {
  return (
    <MasterDataTreeContext.Consumer>
      {({ filters, updateFilter }) => (
        <div className='filtersbar'>
          <div className='filters'>
            <label>
              <input
                type='checkbox'
                checked={filters.ignore_assigned}
                onChange={(ev) => updateFilter({ ignore_assigned: ev.target.checked })}
              />
              Excluir pasos ya asignados
            </label>
          </div>
        </div>
      )}
    </MasterDataTreeContext.Consumer>
  )
}

const hasUncheckableItems = (item: HojaTrabajoTreeItem) => {
  if (item.type == 'paso') {
    return _.some(item.items, (subitem) => subitem.user_id != null)
  } else {
    let found = false
    _.each(item.items, (subitem) => {
      if (hasUncheckableItems(subitem)) {
        found = true
        return true
      }
    })
    return found
  }
}

type MasterDataTreeContextType = {
  tree: HojaTrabajoTreeItem[]
  filters: HojaTrabajoFilters
  updateTree: (item: any) => void
  updateFilter: (filter: any) => void
}

type ModalPasoMassAssignState = {
  open: boolean
  step: number
  user_id: string
  tree: HojaTrabajoTreeItem[]
  filters: HojaTrabajoFilters
  pasosChecked: string[]
}

type HojaTrabajoFilters = {
  ignore_assigned: boolean
}

export class ModalPasoMassAssign extends Component<{}, ModalPasoMassAssignState> {
  constructor(props: {}) {
    super(props)

    this.state = {
      open: false,
      step: 1,
      user_id: null,
      tree: null,
      filters: { ignore_assigned: false },
      pasosChecked: []
    }
  }
  componentDidMount() {
    this.setState({
      tree: parseTreeToItems(app.vars.plan_auditoria.fases)
    })
  }

  onSelectUser = (ev: React.SyntheticEvent, data: any) => {
    this.setState({ user_id: data.value })
  }

  onClickToContinueToStep2 = () => {
    if (!this.state.user_id) {
      bootbox.alert('Seleccione un usuario antes de continuar.')
      return
    }

    this.setState({ step: 2 })
  }

  updateTree(item: any) {
    this.setState((state) => {
      let tree = Object.assign({}, state.tree)

      let parents = []
      let current = item

      while (current.parent) {
        parents.push(current.parent)
        current = current.parent
      }

      let tree_tmp = tree
      while (parents.length > 0) {
        current = parents.pop()
        let _item = _.find(tree_tmp, { id: current.id })
        tree_tmp = _item.items as HojaTrabajoTreeItem[]
      }

      let found = _.find(tree_tmp, { id: item.id })
      found.selected = item.selected

      if (found.items) {
        this.setItemsChecked(found.items, found.selected)
      }

      this.fixIncompletedCheckedItem(tree)

      const pasosChecked = this.findPasosChecked(tree)

      return { tree, pasosChecked }
    })
  }

  updateFilter(filter: any) {
    this.setState(
      (state) => {
        let filters = Object.assign({}, state.filters, filter)

        return { filters }
      },
      () => {
        let tree = Object.assign({}, this.state.tree)
        this.refreshItemsChecked()
        this.fixIncompletedCheckedItem(tree)
        this.setState({ tree })
      }
    )
  }

  setItemsChecked(items: any[], checked: boolean = true) {
    _.each(items, (item) => {
      item.selected = checked && this.isItemCheckable(item)
      if (item.items) {
        this.setItemsChecked(item.items, checked)
      }
    })
  }

  findPasosChecked(items: HojaTrabajoTreeItem[]) {
    let pasosChecked: string[] = []
    _.each(items, (item) => {
      if (item.type == 'pasovisita') {
        if (item.selected) {
          pasosChecked.push(item.id)
        }
      } else {
        pasosChecked = _.union(pasosChecked, this.findPasosChecked(item.items as HojaTrabajoTreeItem[]))
      }
    })

    return pasosChecked
  }

  isItemCheckable(item: any) {
    if (item.type == 'pasovisita' && item.user_id && this.state.filters.ignore_assigned) {
      return false
    }

    return true
  }

  refreshItemsChecked() {
    const refreshItems = (items: any[]) => {
      _.each(items, (item) => {
        if (item.type == 'pasovisita') {
          item.selected = item.selected && this.isItemCheckable(item)
        } else if (item.items) {
          refreshItems(item.items)
        }
      })
    }

    refreshItems(this.state.tree)
  }

  fixIncompletedCheckedItem(items: any) {
    const checkChildren = (items: any) => {
      _.each(items, (item) => {
        if (!item.items) {
          checkParent(item.parent)
        } else {
          checkChildren(item.items)
        }
      })
    }

    const checkParent = (item: any) => {
      const checkableItems = _.filter(item.items, (_item: HojaTrabajoTreeItem) => {
        return this.isItemCheckable(_item)
      })

      const uncheckedCount = _.size(_.filter(checkableItems, { selected: false }))
      item.selected = _.size(item.items) > 0 && _.size(checkableItems) > 0 && uncheckedCount == 0

      if (item.parent) {
        checkParent(item.parent)
      }
    }

    checkChildren(items)
  }

  renderStepContent() {
    const { step } = this.state

    switch (step) {
      case 1:
        const equipoChoices: any = _.map(app.vars.plan_auditoria.equipo, function(item: any) {
          return {
            key: item.user_id,
            text: item.fullname,
            value: item.user_id,
            description: item.rol.name,
            label: { content: item.sigla, circular: true, color: 'green' }
          }
        })

        return (
          <div className='step-content  step-content-1'>
            <h4 className='ui centered header'>Seleccione el usuario al cual desea realizar la asignacin de pasos</h4>
            <div className='ui field centered user-dropdown-wrapper'>
              <Dropdown
                placeholder='Seleccione el usuario'
                defaultValue={this.state.user_id}
                selection
                fluid
                options={equipoChoices}
                onChange={this.onSelectUser}
              />
            </div>
            <div className='btn-actions'>
              <button className='btn btn-primary pull-right' onClick={this.onClickToContinueToStep2}>
                Continuar
              </button>
            </div>
          </div>
        )
      case 2:
        const user = ProyectoPlanAuditoriaUtils.getUserById(this.state.user_id)
        const context = {
          tree: this.state.tree,
          filters: this.state.filters,
          updateTree: this.updateTree.bind(this),
          updateFilter: this.updateFilter.bind(this)
        }

        return (
          <div className='step-content  step-content-2'>
            <h4 className='ui centered header'>
              Seleccione los pasos los cuales desea asignarle al usuario:
              <strong className='selected-user'>
                {user.fullname} ({user.sigla})
              </strong>
            </h4>
            <MasterDataTreeContext.Provider value={context}>
              <Filters />
              <HojaTrabajoTree />
            </MasterDataTreeContext.Provider>

            <div className='btn-actions'>
              <button className='btn ' onClick={() => this.setState({ step: 1 })}>
                Volver
              </button>
              <Popup
                trigger={
                  <button
                    className='btn btn-primary pull-right'
                    onClick={this.onSubmit}
                    disabled={this.state.pasosChecked.length == 0}
                  >
                    Guardar
                  </button>
                }
                content='Seleccione por lo menos un paso al cual asignarle el usuario responsable'
                disabled={this.state.pasosChecked.length > 0}
                on='hover'
                className='disabled-text'
              />
            </div>
          </div>
        )
    }
  }

  onClose = () => {
    this.setState({ open: false })
  }

  onSubmit = () => {
    $.blockUI(create_blockUI_loading_message('Guardando los cambios... Por Favor Espere'))
    $.post(url_for(`proyecto/${app.vars.plan_auditoria.proyecto.id}/mass_assign_user`), {
      user_id: this.state.user_id,
      pasos: this.state.pasosChecked
    }).done(() => {
      location.reload()
    })
  }

  render() {
    const { step } = this.state

    const onClickMenuItem = (ev: SyntheticEvent) => {
      ev.preventDefault()
      this.setState({ open: true })
    }

    return (
      <Fragment>
        <a href='#' onClick={onClickMenuItem}>
          Asignar Responsables
        </a>

        {this.state.open && (
          <Modal open className='modal-ab modal-pasomassassign' closeIcon onClose={this.onClose}>
            <Modal.Header content='Asignacin masiva de usuarios a pasos' />
            <Modal.Content>
              <div className='stepbar'>
                <Step.Group attached='top' size='small' widths={2}>
                  <Step active={step == 1} completed={step > 1} icon={step > 1 ? 'check' : null} title='1. Usuario' />
                  <Step active={step == 2} title='2. Pasos' />
                </Step.Group>
              </div>

              {this.renderStepContent()}
            </Modal.Content>
          </Modal>
        )}
      </Fragment>
    )
  }
}
