import { Component } from 'react'

import PlanAuditoriaContext from './PlanAuditoriaContext'
import { AppTopBar, AppBottomBar } from './AppBars'
import AppTree from './AppTree'

import cx from 'classnames'

declare var app: any

type AppState = {
  tree: any
  filters: any
  visitas: any
}

export default class App extends Component<{}, AppState> {
  constructor(props: {}) {
    super(props)

    this.state = {
      tree: null,
      visitas: app.vars.plan_auditoria.visitas,
      filters: Object.assign({}, app.vars.plan_auditoria.settings.filters)
    }
  }

  updateTree = (tree: any, cb?: () => void) => {
    this.setState(() => {
      return { tree }
    }, cb)
  }

  updateVisitas = (visitas: any, cb?: () => void) => {
    this.setState(() => {
      return { visitas }
    }, cb)
  }

  updateFilters = (newfilters: any) => {
    this.setState(
      (state: AppState) => {
        let filters = newfilters === null ? {} : Object.assign({}, state.filters, newfilters)

        _.each(filters, (value: string, filter) => {
          if (value === '') {
            delete filters[filter]
          }
        })

        return { filters }
      },
      () => {
        let filters: any = {}
        _.each(this.state.filters, (value, key) => {
          if (value && value !== '') {
            filters[key] = value
          }
        })

        $.post(url_for('proyecto/' + app.vars.idproyecto + '/master_data/setfilters'), { filters })
      }
    )
  }

  render() {
    const contextValue = {
      tree: this.state.tree,
      visitas: this.state.visitas,
      filters: this.state.filters,
      updateTree: this.updateTree,
      updateFilters: this.updateFilters,
      updateVisitas: this.updateVisitas
    }

    const appClasses = cx('app-body', _.size(this.state.filters) > 0 ? 'has-filters' : null)

    return (
      <PlanAuditoriaContext.Provider value={contextValue}>
        <div className={appClasses}>
          <AppTopBar />
          <AppTree />
          <AppBottomBar />
        </div>
      </PlanAuditoriaContext.Provider>
    )
  }
}
