import { Component, Fragment, SyntheticEvent, useContext, PureComponent, createRef } from 'react'

import PlanAuditoriaContext from './PlanAuditoriaContext'
import { AppFilters, AppFilterMessage } from './AppFilters'
import { ProyectoPlanAuditoriaUtils } from './utils'
import { ModalPasoMassAssign } from './ModalPasoAssign'
import { AppActionVisitasManage } from './ModalVisitasManage'

declare var app: any

const VisibilityLinkActions = () => {
  const { tree, updateTree } = useContext(PlanAuditoriaContext)

  const onClickExpand = (ev: SyntheticEvent) => {
    ev.preventDefault()
    $.blockUI()
    setTimeout(() => {
      ProyectoPlanAuditoriaUtils.changeOpenToAll(tree, true)
      updateTree(tree)
      $.unblockUI()
    }, 100)
  }

  const onClickCollapse = (ev: SyntheticEvent) => {
    ev.preventDefault()
    ProyectoPlanAuditoriaUtils.changeOpenToAll(tree, false)
    updateTree(tree)
  }

  return (
    <Fragment>
      <a href='#' className='expandall' onClick={onClickExpand}>
        Expandir todo
      </a>
      |
      <a href='#' className='collapseall' onClick={onClickCollapse}>
        Contraer todo
      </a>
    </Fragment>
  )
}

class AppActionCreateFase extends PureComponent {
  static contextType = PlanAuditoriaContext

  onClick = (ev: SyntheticEvent) => {
    ev.preventDefault()

    const { tree, updateTree } = this.context

    const prompt = bootbox.prompt(
      { header: 'Nueva Fase', content: '<div>Escriba el nombre de la fase que desea crear.</div>' },
      'Cancelar',
      'Crear',
      (r: string) => {
        if (!r || _.str.trim(r) == '') {
          return
        }

        $.blockUI(create_blockUI_loading_message('Creando Fase... Por Favor Espere'))
        $.post(url_for(`proyecto/${app.vars.idproyecto}/master_data/fase`), { fase_name: r })
          .done((res) => {
            if (!res.hasErrors) {
              const fase = res.response.record
              tree.push({
                id: fase.id_master_data_fase,
                code: fase.code,
                descripcion: fase.descripcion,
                position: fase.position,
                index: tree.length,
                sortIndex: tree.length,
                open: true,
                items: []
              })
              updateTree(tree)
              prompt.modal('hide')
            }

            $.unblockUI()
          })
          .fail(() => {
            $.pnotify({ type: 'error', text: 'Un error ocurri al intentar crear la fase.' })
            $.unblockUI()
          })

        return false
      },
      ''
    )
    $('.bootbox.modal').addClass('bootbox-prompt modal-ab')
  }

  render() {
    return (
      <a href='#' onClick={this.onClick}>
        Crear Fase
      </a>
    )
  }
}

class AppTopBarActions extends PureComponent {
  static contextType = PlanAuditoriaContext

  render() {
    const { tree } = this.context
    if (tree === null) {
      return (
        <div className='pull-right' style={{ position: 'relative' }}>
          <a className='btn btn-mini dropdown-toggle btn-new pull-right' data-toggle='dropdown' href='#'>
            <div className='ui mini active inline loader' />
          </a>
        </div>
      )
    }

    const is_editable = app.vars.plan_auditoria.proyecto.isOpen

    return (
      <div className='pull-right' style={{ position: 'relative' }}>
        <a className='btn btn-mini dropdown-toggle btn-new pull-right' data-toggle='dropdown' href='#'>
          Opciones
        </a>
        <ul className='dropdown-menu pull-right' style={{ textAlign: 'left' }}>
          {is_editable &&
          user_has_role_permission('fase_create') && (
            <li>
              <AppActionCreateFase />
            </li>
          )}
          {is_editable &&
          user_has_role_permission('visita_edit') && (
            <li>
              <AppActionVisitasManage />
            </li>
          )}
          {is_editable &&
          user_has_role_permission('paso_asignar_usuario') && (
            <li>
              <ModalPasoMassAssign />
            </li>
          )}
          <li>
            <a href={url_for(`proyecto/${app.vars.idproyecto}/master_data?sf_format=pdf`)} target='_blank'>
              Exportar como PDF
            </a>
          </li>
        </ul>
      </div>
    )
  }
}

export class AppTopBar extends PureComponent {
  scrollboxRef = createRef<HTMLDivElement>()

  componentDidMount() {
    const $stickyBox = $(this.scrollboxRef.current)
    const stickyBoxTop = $stickyBox.offset().top
    $(window).scroll(function() {
      var y = $(this).scrollTop()
      if (y >= stickyBoxTop) {
        $stickyBox.addClass('fixed')
      } else {
        $stickyBox.removeClass('fixed')
      }
    })
  }

  render() {
    return (
      <div className='topbar sticky-scroll-box' ref={this.scrollboxRef}>
        <div className='ui grid'>
          <div className='five wide column'>
            <VisibilityLinkActions />
          </div>
          <div className='nine wide column'>
            <AppFilters />
          </div>
          <div className='two wide column'>
            <AppTopBarActions />
          </div>
        </div>
        <AppFilterMessage />
      </div>
    )
  }
}

export class AppBottomBar extends Component {
  render() {
    return (
      <div className='bottombar'>
        <VisibilityLinkActions />
      </div>
    )
  }
}
