import { Component, SyntheticEvent, Fragment, PureComponent } from 'react'

import Modal from 'semantic-ui-react/dist/commonjs/modules/Modal/Modal'
import Step from 'semantic-ui-react/dist/commonjs/elements/Step/Step'
import Label from 'semantic-ui-react/dist/commonjs/elements/Label/Label'
import Button from 'semantic-ui-react/dist/commonjs/elements/Button/Button'
import Icon from 'semantic-ui-react/dist/commonjs/elements/Icon/Icon'
import Grid from 'semantic-ui-react/dist/commonjs/collections/Grid/Grid'
import Form from 'semantic-ui-react/dist/commonjs/collections/Form/Form'
import Segment from 'semantic-ui-react/dist/commonjs/elements/Segment/Segment'
import Radio from 'semantic-ui-react/dist/commonjs/addons/Radio/Radio'
import Popup from 'semantic-ui-react/dist/commonjs/modules/Popup/Popup'

import DualListBox from 'react-dual-listbox'
import PlanAuditoriaContext from './PlanAuditoriaContext'

declare var app: any

const DualListBoxIcons = {
  moveLeft: <i className='icon angle left' />,
  moveAllLeft: <i className='icon angle double left' />,
  moveRight: <i className='icon angle right' />,
  moveAllRight: <i className='icon angle double right' />,
  moveDown: <i className='icon angle down' />,
  moveUp: <i className='icon angle up' />
}

// ******************** STEP 1 *********************************
/**
 *
 */
class ModalPasoStep1 extends Component<{ onSelect: (pasoTipo: ProyectoPasoTipo) => void }> {
  render() {
    return (
      <div className='step-content step-content-1'>
        <h4 className='ui centered header'>Qu tipo de paso de auditora desea crear?</h4>
        <Grid relaxed centered columns={4} textAlign='center'>
          <Grid.Column>
            <Segment textAlign='center' centered className='paso-tipo' onClick={() => this.props.onSelect('DEFAULT')}>
              <Icon name='list ul' size='big' />
              <div className='paso-tipo-name'>
                Lista de Chequeo
                <Popup
                  size='tiny'
                  trigger={<Icon color='blue' size='small' name='question circle' />}
                  header='Paso - Tipo Lista de Chequeo'
                  content={`Relacin de instrucciones o actividades definidas como gua detallada
                  para ser ejecutadas por el auditor permitiendo una revisin efectiva,
                  por lo general contienen el paso a paso de un programa de trabajo para realizar pruebas sustantivas
                  (prueba de detalle, revisiones analticas, etc).`}
                />
              </div>
            </Segment>
          </Grid.Column>
          <Grid.Column>
            <Segment
              textAlign='center'
              disabled={app.vars.plan_auditoria.proyecto.version == '1'}
              className='paso-tipo'
              onClick={() => {
                if (app.vars.plan_auditoria.proyecto.version == '1') {
                  bootbox.alert(
                    'Esta opcin solo est disponible para los proyectos actualizados con la nueva versin de la Hoja de Trabajo.'
                  )
                } else {
                  return this.props.onSelect('PERIODO')
                }
              }}
            >
              <Icon name='calendar alternate outline' size='big' />
              <div className='paso-tipo-name'>
                Impuestos
                <Popup
                  size='tiny'
                  trigger={<Icon color='blue' size='small' name='question circle' />}
                  header='Paso - Tipo Impuesto'
                  content={`Programa de trabajo para la revisin peridica de impuestos por parte del auditor,
                  es requerido crear un paso de impuestos por cada impuesto a verificar.
                  La principal diferencia con un paso de lista de chequeo consiste que los impuestos
                  se vinculan a periodos de vencimientos y las listas de chequeo se vinculan a
                  visitas fuerte de auditoria (planeacin,prefinal, final, etc.)
                  por tanto, para pasos de impuestos no es requerido subir cifras en sumarias.`}
                />
              </div>
            </Segment>
          </Grid.Column>
          <Grid.Column>
            <Segment textAlign='center' disabled className='paso-tipo comingsoon'>
              <Label color='orange' ribbon='right' size='medium'>
                Prximamente
              </Label>
              <div className='clearfix' />
              <Icon name='file alternate outline' size='big' />
              <div className='paso-tipo-name'>Formatos</div>
            </Segment>
          </Grid.Column>
        </Grid>
        <br />
      </div>
    )
  }
}

declare type ModalPasoStepDefaultProps = {
  value: any
  onChange: (value: any) => void
  onContinue: () => void
  onCancel: () => void
}

// ******************** STEP 2 *********************************
/**
 *
 */
class ModalPasoStep2 extends Component<ModalPasoStepDefaultProps> {
  onChange = (e: any) => {
    this.props.onChange(e.target.value)
  }

  onSubmit = (ev: SyntheticEvent) => {
    ev.preventDefault()
    this.props.onContinue()
  }

  render() {
    return (
      <div className='step-content step-content-2'>
        <Form onSubmit={this.onSubmit}>
          <Grid centered textAlign='center'>
            <Grid.Column width={10}>
              <Form.Field required label='Ingrese el nombre del paso' />
              <Form.Input
                type='text'
                name='paso_name'
                placeholder='Nombre del paso'
                defaultValue={this.props.value}
                onChange={this.onChange}
                maxLength={255}
                required
              />
            </Grid.Column>
          </Grid>
          <br />
          <Button negative content='Volver' onClick={this.props.onCancel} type='button' />
          <Button positive content='Continuar' floated='right' />
        </Form>
      </div>
    )
  }
}

// ******************** STEP 3 *********************************

/**
 *
 */
class ModalPasoStep3 extends Component<ModalPasoStepDefaultProps, { value: string[] }> {
  constructor(props: ModalPasoStepDefaultProps) {
    super(props)

    this.state = {
      value: this.props.value
    }
  }

  onChange = (selected: string[]) => {
    this.setState({ value: selected })
    this.props.onChange(selected)
  }

  onSubmit = (ev: SyntheticEvent) => {
    ev.preventDefault()
    this.props.onContinue()
  }

  render() {
    let options: { value: string; label: string }[] = []
    _.forEach(app.vars.plan_auditoria.objetivos, function(item: ProyectoObjetivo) {
      options.push({ value: item.id, label: item.name })
    })

    return (
      <div className='step-content step-content-3'>
        <Form onSubmit={this.onSubmit}>
          <Grid centered textAlign='center'>
            <Grid.Column width={10}>
              <Form.Field label='Asocie las aserciones a relacionar con este paso' /> <br />
              <DualListBox
                name='aserciones'
                options={options}
                availableLabel='Por asociar'
                selectedLabel='Asociadas'
                selected={this.state.value}
                icons={DualListBoxIcons}
                onChange={this.onChange}
              />
            </Grid.Column>
          </Grid>
          <br />
          <Button negative content='Volver' onClick={this.props.onCancel} type='button' />
          <Button positive content='Continuar' floated='right' />
        </Form>
      </div>
    )
  }
}

// ******************** STEP 4 *********************************

