/**
 *
 */
class ModalPasoStep4Periodo extends Component<ModalPasoStepDefaultProps, { value: string }> {
  constructor(props: ModalPasoStepDefaultProps) {
    super(props)

    this.state = {
      value: this.props.value
    }
  }

  onChange = (e: SyntheticEvent, { value }: any) => {
    this.setState({ value })
    this.props.onChange(value)
  }

  onSubmit = (ev: SyntheticEvent) => {
    ev.preventDefault()
    this.props.onContinue()
  }

  render() {
    const options: { text: string; value: string; help?: string }[] = [
      { value: '1', text: 'Mensual', help: 'Enero, Febrero, Marzo, ...' },
      { value: '2', text: 'Bimestral', help: 'Bimestre 1, Bimestre 2, ...' },
      { value: '3', text: 'Trimestral', help: 'Trimestre 1, Trimestre 2, ...' },
      { value: '4', text: 'Cuatrimestral', help: 'Cuatrimestre 1, Cuatrimestre 2, ...' },
      { value: '6', text: 'Semestral', help: 'Semestre 1, Semestre 2' },
      { value: '12', text: 'Anual', help: 'Ao actual' }
    ]

    return (
      <div className='step-content step-content-4'>
        <Form onSubmit={this.onSubmit}>
          <Grid centered textAlign='center'>
            <Grid.Column width={10}>
              <Form.Field label='Indique la periodicidad para este paso' />
              {_.map(options, (option, k) => {
                return (
                  <Form.Field className='field-periodo'>
                    <Radio
                      label={
                        <label>
                          {option.text} <span className='help help-inline'>{option.help}</span>
                        </label>
                      }
                      name='periodo'
                      value={option.value}
                      checked={this.state.value == option.value}
                      onChange={this.onChange}
                    />
                  </Form.Field>
                )
              })}
            </Grid.Column>
          </Grid>
          <br />
          <Button negative content='Volver' onClick={this.props.onCancel} type='button' />
          <Button positive content='Crear Paso' floated='right' />
        </Form>
      </div>
    )
  }
}

// ******************** PASO NEW *********************************
declare type ModalPasoNewProps = {
  rubro: ItemSubfase
}

declare type ModalPasoNewState = {
  step: number
  isOpen: boolean
  pasoTipo: ProyectoPasoTipo | null
  formData: any
}

export class ModalPasoNew extends Component<ModalPasoNewProps, ModalPasoNewState> {
  constructor(props: ModalPasoNewProps) {
    super(props)

    this.state = {
      step: 1,
      pasoTipo: null,
      isOpen: false,
      formData: {
        paso_name: '',
        aserciones: []
      }
    }
  }

  nextStep = () => {
    this.setState((prevState) => ({
      step: prevState.step + 1
    }))
  }

  prevStep = () => {
    this.setState((prevState) => ({
      step: prevState.step - 1
    }))
  }

  selectPasoTipo = (pasoTipo: ProyectoPasoTipo) => {
    this.setState({ step: 2, pasoTipo })
  }

  _assignFormDataValue(data: any) {
    this.setState((prevState) => {
      let { formData } = prevState
      formData = Object.assign({}, formData, data)
      return { formData }
    })
  }

  assignPasoName = (paso_name: string) => {
    this._assignFormDataValue({ paso_name })
  }

  assignAserciones = (aserciones: string[]) => {
    this._assignFormDataValue({ aserciones })
  }

  assignVisitas = (visitas: string[]) => {
    this._assignFormDataValue({ visitas, periodos: null })
  }

  assignPeriodos = (periodos: string[]) => {
    this._assignFormDataValue({ visitas: null, periodos })
  }

  submit = () => {
    $.blockUI(create_blockUI_loading_message('Creando Paso'))

    let form = document.createElement('form')
    form.method = 'POST'
    form.action = url_for(
      `proyecto/${app.vars.plan_auditoria.proyecto.id}/master_data/fase/${this.props.rubro.id}/paso/newcreate`
    )

    let input = document.createElement('input')
    input.name = 'tipo'
    input.value = this.state.pasoTipo
    input.type = 'hidden'
    form.appendChild(input)

    _.forEach(this.state.formData, function(value, key) {
      let input = document.createElement('input')
      input.name = `data[${key}]`
      input.value = value
      input.type = 'hidden'
      form.appendChild(input)
    })

    document.body.appendChild(form)

    form.submit()
  }

  onClose = () => {
    this.setState({ isOpen: false })
  }

  _getStepContent(step: number) {
    switch (step) {
      case 1:
        return <ModalPasoStep1 onSelect={this.selectPasoTipo} />
      case 2:
        return (
          <ModalPasoStep2
            value={this.state.formData.paso_name}
            onCancel={this.prevStep}
            onChange={this.assignPasoName}
            onContinue={this.nextStep}
          />
        )
      case 3:
        return (
          <ModalPasoStep3
            value={this.state.formData.aserciones}
            onCancel={this.prevStep}
            onChange={this.assignAserciones}
            onContinue={this.nextStep}
          />
        )
      case 4:
        switch (this.state.pasoTipo) {
          case 'DEFAULT':
            return (
              <ModalPasoStep4Default
                value={this.state.formData.visitas}
                onCancel={this.prevStep}
                onChange={this.assignVisitas}
                onContinue={this.submit}
              />
            )
          case 'PERIODO':
            return (
              <ModalPasoStep4Periodo
                value={this.state.formData.periodos}
                onCancel={this.prevStep}
                onChange={this.assignPeriodos}
                onContinue={this.submit}
              />
            )
        }
    }
  }

  render() {
    const { step, pasoTipo, isOpen } = this.state

    let step4Title = ' '
    if (step > 1) {
      step4Title = pasoTipo == 'PERIODO' ? '4. Periodos' : '4. Visitas'
    }

    const { rubro } = this.props

    return (
      <Fragment>
        <a href='#' onClick={() => this.setState({ isOpen: true })}>
          Crear Paso
        </a>
        {isOpen && (
          <Modal open className='modal-ab modal-pasocreate' onClose={this.onClose} closeIcon>
            <Modal.Header content='Crear paso' />
            <Modal.Content>
              <p>
                <strong>Rubro:</strong> {rubro.descripcion}
              </p>
              <Step.Group attached='top' size='small' widths={4}>
                <Step active={step == 1} completed={step > 1} icon={step > 1 ? 'check' : ' '} title='1. Tipo' />
                <Step active={step == 2} completed={step > 2} icon={step > 2 ? 'check' : ' '} title='2. Nombre' />
                <Step active={step == 3} completed={step > 3} icon={step > 3 ? 'check' : ' '} title='3. Aserciones' />
                <Step active={step == 4} title={step4Title} icon=' ' />
              </Step.Group>
              {this._getStepContent(step)}
            </Modal.Content>
          </Modal>
        )}
      </Fragment>
    )
  }
}

type ModalPasoEditState = {
  isOpen: boolean
  valueName: string
  valueAserciones: string[]
}

export class ModalPasoEdit extends PureComponent<{ paso: ItemPaso }, ModalPasoEditState> {
  static contextType = PlanAuditoriaContext

  constructor(props: { paso: ItemPaso }) {
    super(props)

    this.state = {
      isOpen: false,
      valueName: props.paso.descripcion,
      valueAserciones: _.pluck(props.paso.aserciones, 'id_master_data_objetivo')
    }
  }

  onChangeName = (ev: React.ChangeEvent) => {
    this.setState({ valueName: (ev.target as any).value })
  }

  onChangeAserciones = (aserciones: string[]) => {
    this.setState({ valueAserciones: aserciones })
  }

  onSubmit = () => {
    if (_.str.trim(this.state.valueName) == '') {
      bootbox.alert('Indique un nombre para el paso.')
      return
    }

    const { tree, updateTree } = this.context
    const { paso } = this.props

    $.blockUI(create_blockUI_loading_message('Guardando los cambios'))

    $.post(url_for(`proyecto/${app.vars.idproyecto}/master_data/paso/${paso.id}`), {
      paso_name: this.state.valueName,
      paso_objetivos: this.state.valueAserciones
    }).done((res) => {
      if (!res.hasErrors) {
        tree[paso.parent.parent.index].items[paso.parent.index].open = true
        tree[paso.parent.parent.index].items[paso.parent.index].items[paso.index].descripcion = this.state.valueName
        tree[paso.parent.parent.index].items[paso.parent.index].items[paso.index].aserciones = _.map(
          this.state.valueAserciones,
          (asercionId) => {
            return { id_master_data_objetivo: asercionId }
          }
        )
        updateTree(tree)
        $.unblockUI()

        this.setState({ isOpen: false })
      }
    })
  }

  render() {
    const { paso } = this.props

    let asercionesoChoices: { value: string; label: string }[] = []
    _.forEach(app.vars.plan_auditoria.objetivos, function(item: ProyectoObjetivo) {
      asercionesoChoices.push({ value: item.id, label: item.name })
    })

    return (
      <Fragment>
        <a href='#' onClick={() => this.setState({ isOpen: true })}>
          Editar Paso
        </a>
        {this.state.isOpen && (
          <Modal open className='modal-ab modal-pasoedit' onClose={() => this.setState({ isOpen: false })} closeIcon>
            <Modal.Header content='Editar paso' />
            <Modal.Content>
              <p>
                <div>
                  <strong>Rubro:</strong> {paso.parent.descripcion}
                </div>
                <div>
                  <strong>Paso:</strong> {paso.descripcion}
                </div>
              </p>

              <div className='ui form'>
                <div className='field required'>
                  <label htmlFor='paso_edit_name'>Nombre del Paso</label>
                  <input
                    type='text'
                    id='paso_edit_name'
                    required
                    value={this.state.valueName}
                    onChange={this.onChangeName}
                    placeholder='Nombre del Paso'
                  />
                </div>

                <div className='field'>
                  <label htmlFor=''>Asocie las aserciones relacionadas con este paso</label>
                  <br />
                  <DualListBox
                    name='aserciones'
                    options={asercionesoChoices}
                    availableLabel='Por asociar'
                    selectedLabel='Asociadas'
                    selected={this.state.valueAserciones}
                    icons={DualListBoxIcons}
                    onChange={this.onChangeAserciones}
                  />
                </div>
              </div>
            </Modal.Content>
            <Modal.Actions>
              <button className='ui button negative' onClick={() => this.setState({ isOpen: false })}>
                Cancelar
              </button>
              <button className='ui button primary' onClick={this.onSubmit}>
                Aceptar
              </button>
            </Modal.Actions>
          </Modal>
        )}
      </Fragment>
    )
  }
}

type ModalPasoAssocState = {
  isOpen: boolean
  visitas: string[]
}

export class ModalPasoAssoc extends PureComponent<{ paso: ItemPaso }, ModalPasoAssocState> {
  static contextType = PlanAuditoriaContext

  constructor(props: { paso: ItemPaso }) {
    super(props)

    this.state = {
      isOpen: false,
      visitas: _.pluck(props.paso.paso_visitas, 'id_proyecto_visita')
    }
  }

  onChange = (visitas: string[]) => {
    this.setState({ visitas })
  }

  onSubmit = () => {
    const { tree, updateTree } = this.context
    const { paso } = this.props

    $.blockUI(create_blockUI_loading_message('Guardando los cambios'))

    $.post(url_for(`proyecto/${app.vars.idproyecto}/master_data/asociar-visita`), {
      id_paso: paso.id,
      paso_visitas: this.state.visitas
    }).done((res) => {
      if (!res.hasErrors) {
        tree[paso.parent.parent.index].items[paso.parent.index].open = true
        tree[paso.parent.parent.index].items[paso.parent.index].items[paso.index].paso_visitas =
          res.response.paso_visitas
        updateTree(tree)
        $.unblockUI()

        this.setState({ isOpen: false })
      }
    })
  }

  render() {
    const { paso } = this.props

    const { visitas } = this.context

    const options: { value: string; label: string }[] = []
    _.forEach(visitas, function(item: ProyectoVisita) {
      options.push({ value: item.id, label: item.name })
    })

    return (
      <Fragment>
        <a href='#' onClick={() => this.setState({ isOpen: true })}>
          Asociar Visitas
        </a>
        {this.state.isOpen && (
          <Modal open className='modal-ab modal-pasoedit' onClose={() => this.setState({ isOpen: false })} closeIcon>
            <Modal.Header content='Editar paso' />
            <Modal.Content>
              <div>
                <div>
                  <strong>Rubro:</strong> {paso.parent.descripcion}
                </div>
                <div>
                  <strong>Paso:</strong> {paso.descripcion}
                </div>
              </div>

              <div className='ui form'>
                <div className='field'>
                  <label htmlFor=''>Asocie las visitas relacionadas con este paso</label>
                  <br />
                  <DualListBox
                    name='aserciones'
                    options={options}
                    availableLabel='Por asociar'
                    selectedLabel='Asociadas'
                    selected={this.state.visitas}
                    icons={DualListBoxIcons}
                    onChange={this.onChange}
                  />
                </div>
              </div>
            </Modal.Content>
            <Modal.Actions>
              <button className='ui button negative' onClick={() => this.setState({ isOpen: false })}>
                Cancelar
              </button>
              <button className='ui button primary' onClick={this.onSubmit}>
                Aceptar
              </button>
            </Modal.Actions>
          </Modal>
        )}
      </Fragment>
    )
  }
}
