declare var app: any

export namespace ProyectoPlanAuditoriaUtils {
  export function getUserById(id: string) {
    return app.vars.plan_auditoria.equipo[id]
  }

  export function getVisitaById(id: string): any {
    return _.find(app.vars.plan_auditoria.visitas, { id })
  }

  export function changeOpenToAll(tree: any, value: boolean) {
    _.each(tree, (item) => {
      if (typeof item.open !== 'undefined') {
        item.open = value
      }

      if (typeof item.items !== 'undefined') {
        changeOpenToAll(item.items, value)
      }
    })
  }
}
