import { Component, SyntheticEvent, useContext } from 'react'

import cx from 'classnames'

import PlanAuditoriaContext from './PlanAuditoriaContext'
import { ProyectoPlanAuditoriaUtils } from './utils'

declare var app: any

export class AppFilters extends Component {
  static contextType = PlanAuditoriaContext

  setFilter(filter: 'visita' | 'status' | 'asignado', value: string) {
    const { tree, updateFilters, updateTree } = this.context
    updateFilters({ [filter]: value })

    $.blockUI()
    setTimeout(() => {
      ProyectoPlanAuditoriaUtils.changeOpenToAll(tree, true)
      updateTree(tree)
      $.unblockUI()
    }, 100)
  }

  renderVisitasFilterChoices() {
    const { filters } = this.context
    const visitas: { id: string; name: string }[] = app.vars.plan_auditoria.visitas

    const onChange = (ev: SyntheticEvent) => {
      this.setFilter('visita', (ev.target as any).value)
    }

    return (
      <select
        className={cx('raw-select input-medium', filters.visita ? 'active' : null)}
        id='master_data_filters_visita'
        style={{ maxWidth: 150 }}
        onChange={onChange}
        value={filters.visita || ''}
      >
        <option value=''>- Visita -</option>
        {_.map(visitas, (visita) => {
          return (
            <option key={visita.id} value={visita.id}>
              {visita.name}
            </option>
          )
        })}
      </select>
    )
  }

  renderStatusFilterChoices() {
    const { filters } = this.context
    const onChange = (ev: SyntheticEvent) => {
      this.setFilter('status', (ev.target as any).value)
    }

    const statuses = [ 'Sin asignar', 'Asignado', 'En proceso', 'Completado', 'Revisado', 'Notas Revisin' ]

    return (
      <select
        className={cx('raw-select input-medium', filters.status ? 'active' : null)}
        id='master_data_filters_status'
        onChange={onChange}
        value={filters.status || ''}
      >
        <option value=''>- Estado -</option>
        {_.map(statuses, (status, key) => {
          return (
            <option key={key + 1} value={key + 1}>
              {status}
            </option>
          )
        })}
      </select>
    )
  }

  renderUsersFilterChoices() {
    const { filters } = this.context
    const equipo: { id: string; fullname: string; user_id: string }[] = app.vars.plan_auditoria.equipo

    const onChange = (ev: SyntheticEvent) => {
      this.setFilter('asignado', (ev.target as any).value)
    }

    return (
      <select
        className={cx('raw-select input-medium', filters.asignado ? 'active' : null)}
        id='master_data_filters_asignado'
        style={{ maxWidth: 150 }}
        onChange={onChange}
        value={filters.asignado || ''}
      >
        <option value=''>- Asignado a -</option>
        {_.map(equipo, (user) => {
          return (
            <option key={user.id} value={user.user_id}>
              {user.fullname}
            </option>
          )
        })}
      </select>
    )
  }

  render() {
    return (
      <div id='master_data_filters' className='pull-right'>
        <span className='lb'>Filtrar por: </span>
        {this.renderVisitasFilterChoices()}
        {this.renderStatusFilterChoices()}
        {this.renderUsersFilterChoices()}
      </div>
    )
  }
}

export const AppFilterMessage = () => {
  const { filters, updateFilters } = useContext(PlanAuditoriaContext)

  const hasFilters = _.reject(filters, (value) => value === '' || !value).length > 0

  if (!hasFilters) {
    return null
  }

  const onReset = (ev: SyntheticEvent) => {
    ev.preventDefault()

    updateFilters(null)
  }

  return (
    <div id='filteralerts' className='alert-2 alert-primary'>
      <span className='icon-info-sign' style={{ marginRight: 3 }} />
      NOTA: La visibilidad de los pasos est afectada por los filtros aplicados.
      <a href='#' className='resetfilters' style={{ marginLeft: 10 }} onClick={onReset}>
        Quitar filtros
      </a>
    </div>
  )
}
