import { PureComponent, Fragment, SyntheticEvent } from 'react'
import PlanAuditoriaContext from './PlanAuditoriaContext'

import Modal from 'semantic-ui-react/dist/commonjs/modules/Modal/Modal'
import List from 'semantic-ui-react/dist/commonjs/elements/List/List'

declare var app: any

type ModalVisitasManageState = {
  open: boolean
}

export class AppActionVisitasManage extends PureComponent<{}, ModalVisitasManageState> {
  state: ModalVisitasManageState = {
    open: false
  }

  onClickAction = (ev: SyntheticEvent) => {
    ev.preventDefault()
    this.setState({ open: true })
  }

  render() {
    return (
      <Fragment>
        <a href='#' onClick={this.onClickAction}>
          Administrar Visitas
        </a>
        {this.state.open && <ModalVisitasManage onClose={() => this.setState({ open: false })} />}
      </Fragment>
    )
  }
}

class VisitaManageItem extends PureComponent<{ visita: ProyectoVisita }> {
  static contextType = PlanAuditoriaContext

  onClickDelete = (event: SyntheticEvent) => {
    event.preventDefault()
    const { visita } = this.props
    let { visitas, updateVisitas } = this.context

    bootbox.warningConfirm(
      'Esta seguro de querer eliminar por completo esta visita?',
      'No',
      'S, Eliminar visita',
      (answer) => {
        if (!answer) {
          return
        }

        $.blockUI(create_blockUI_loading_message('Eliminando Visita...'))
        $.post(url_for(`proyecto/${app.vars.idproyecto}/master_data/visita/${visita.id}`), { sf_method: 'delete' })
          .done(() => {
            visitas = _.reject(visitas, { id: visita.id })
            updateVisitas(visitas)
            $.unblockUI()
          })
          .fail(() => $.unblockUI())
      }
    )
  }

  onClickEdit = (event: SyntheticEvent) => {
    event.preventDefault()

    const { visita } = this.props
    let { visitas, updateVisitas } = this.context

    const prompt = bootbox.prompt(
      {
        header: `Renombrar Visita - ${visita.name}`,
        content: '<div>Escriba el nuevo nombre que desea asignarle a la visita seleccionada.</div>'
      },
      'Cancelar',
      'Actualizar',
      (r: string) => {
        if (!r || _.str.trim(r) == '') {
          return
        }

        $.blockUI(create_blockUI_loading_message('Actualizando Visita...'))
        $.post(url_for(`proyecto/${app.vars.idproyecto}/master_data/visita/${visita.id}`), { visita_name: r })
          .done((res) => {
            if (!res.hasErrors) {
              const _visita: any = _.findWhere(visitas, { id: visita.id })
              _visita.name = res.response.record.name
              _visita.position = res.response.record.position
              _visita.sumaria_periodo_actual = res.response.record.sumaria_periodo_actual

              updateVisitas(visitas)
              prompt.modal('hide')

              $.unblockUI()
            } else {
              $.unblockUI()
              bootbox.alert(
                `<p>El siguiente ocurri al intentar actualizar la visita:</p>
                <div class="alert alert-error alert-block _a-center">${res.message}</div>`
              )
            }
          })
          .fail(() => {
            $.pnotify({ type: 'error', text: 'Un error inesperado ocurri al intentar actualizar la visita.' })
            $.unblockUI()
          })

        return false
      },
      visita.name
    )
    $('.bootbox.modal').addClass('bootbox-prompt modal-ab')
  }

  render() {
    const { visita } = this.props
    const is_editable = app.vars.plan_auditoria.proyecto.isOpen

    return (
      <List.Item id={`visita-li-${visita.id}`} className='visita-listitem'>
        <span className='sort-handler drag-icon' />
        <List.Content style={{ display: 'inline-block' }}>{visita.name}</List.Content>
        {is_editable && (
          <List.Content floated='right'>
            {user_has_role_permission('visita_edit') && (
              <Fragment>
                <button className='ui tertiary button compact mini btnedit' onClick={this.onClickEdit}>
                  Cambiar nombre
                </button>
                <button className='ui tertiary button negative compact mini' onClick={this.onClickDelete}>
                  Eliminar
                </button>
              </Fragment>
            )}
          </List.Content>
        )}
      </List.Item>
    )
  }
}

export class ModalVisitasManage extends PureComponent<{ onClose?: () => void }> {
  static contextType = PlanAuditoriaContext

  componentDidMount() {
    ;($('#visitas-adminlist') as any).sortable({
      handle: '.sort-handler',
      items: '.visita-listitem',
      axis: 'y',
      placeholder: 'sortable_placeholder',
      update: this.onSortEnd
    })
  }

  onSortEnd = () => {
    const { visitas, updateVisitas } = this.context

    let ids = ($('#visitas-adminlist') as any).sortable('toArray')
    ids = ids.map(function(id: string) {
      return id.substr(10)
    })

    $.post(url_for(`proyecto/${app.vars.idproyecto}/master_data/visita/sort`), { sort: ids })

    _.each(ids, (id, index: number) => {
      const visitaIndex = _.findIndex(visitas, { id })
      visitas[visitaIndex].position = index + 1
    })

    updateVisitas(visitas)
  }

  onClickNew = (ev: SyntheticEvent) => {
    ev.preventDefault()

    const { visitas, updateVisitas, tree, updateTree } = this.context

    const prompt = bootbox.prompt(
      { header: 'Nueva Visita', content: '<div>Escriba el nombre de la visita que desea crear.</div>' },
      'Cancelar',
      'Crear',
      (r: string) => {
        if (!r || _.str.trim(r) == '') {
          return
        }

        $.blockUI(create_blockUI_loading_message('Creando Visita...'))
        $.post(url_for(`proyecto/${app.vars.idproyecto}/master_data/create-visita`), { visita_name: r })
          .done((res) => {
            if (!res.hasErrors) {
              const visita = res.response.record
              visitas.push({
                id: visita.id_proyecto_visita,
                id_proyecto_visita: visita.id_proyecto_visita,
                name: visita.name,
                position: visita.position,
                sumaria_periodo_actual: visita.sumaria_periodo_actual
              })
              updateVisitas(visitas)
              prompt.modal('hide')

              // Actualiza las visitas asociadas al paso de Materialidad
              const paso_materialidad = res.response.paso_materialidad
              let _pasoFound = false
              _.each(tree, (fase) => {
                _.each(fase.items, (subfase) => {
                  _.each(subfase.items, (paso) => {
                    if (paso.id == paso_materialidad.id) {
                      paso.paso_visitas = paso_materialidad.MasterDataPasoHasProyectoVisita
                      _pasoFound = true
                      return false
                    }
                  })
                  if (_pasoFound) {
                    return false
                  }
                })
                if (_pasoFound) {
                  return false
                }
              })
              updateTree(tree)

              $.unblockUI()
            } else {
              $.unblockUI()
              bootbox.alert(
                `<p>El siguiente ocurri al intentar crear la visita:</p>
                <div class="alert alert-error alert-block _a-center">${res.message}</div>`
              )
            }
          })
          .fail(() => {
            $.pnotify({ type: 'error', text: 'Un error inesperado ocurri al intentar crear la visita.' })
            $.unblockUI()
          })

        return false
      },
      ''
    )
    $('.bootbox.modal').addClass('bootbox-prompt modal-ab')
  }

  render() {
    const { visitas } = this.context

    const is_editable = app.vars.plan_auditoria.proyecto.isOpen

    return (
      <Modal open closeIcon className='modal-ab' onClose={this.props.onClose} size='small'>
        <Modal.Header content='Administracin de Visitas' />
        <Modal.Content>
          {_.size(visitas) > 0 ? (
            <Fragment>
              <p className='description-text'>Listado de visitas de este proyecto:</p>
              <List divided id='visitas-adminlist'>
                {_.map(visitas, (visita: ProyectoVisita) => {
                  return <VisitaManageItem key={visita.id} visita={visita} />
                })}
              </List>
            </Fragment>
          ) : (
            <div className='alert alert-block'>
              <span className='icon-warning-sign' /> Aun no hay visitas creadas en este proyecto.
            </div>
          )}
          <div className='_a-center'>
            {is_editable &&
            user_has_role_permission('visita_create') && (
              <a href='#' className='btn btn-new' style={{ marginRight: 5 }} onClick={this.onClickNew}>
                Nueva Visita
              </a>
            )}
            <button type='button' className='btn btn-danger' onClick={this.props.onClose}>
              Cerrar
            </button>
          </div>
        </Modal.Content>
      </Modal>
    )
  }
}
