import { Component, useState, SyntheticEvent, Fragment } from 'react'

import Modal from 'semantic-ui-react/dist/commonjs/modules/Modal/Modal'
import Grid from 'semantic-ui-react/dist/commonjs/collections/Grid/Grid'
import Loader from 'semantic-ui-react/dist/commonjs/elements/Loader/Loader'
import Dimmer from 'semantic-ui-react/dist/commonjs/modules/Dimmer/Dimmer'
import Popup from 'semantic-ui-react/dist/commonjs/modules/Popup/Popup'

import cx from 'classnames'

declare var app: any

const ModalStep1 = ({ onSelect }: { onSelect: (value: 'MasterData' | 'Project') => void }) => {
  return (
    <div className='step-content step-content-1'>
      <h4 className='ui centered header'>Seleccione el origen del paso de auditora que desea copiar?</h4>
      <Grid relaxed centered columns={4} textAlign='center'>
        <Grid.Column>
          <div className='ui segment center aligned paso-copysource clickable' onClick={() => onSelect('MasterData')}>
            <div className='paso-source-name'>Desde Plan de Pruebas</div>
          </div>
        </Grid.Column>
        <Grid.Column>
          <div className='ui segment center aligned paso-copysource clickable' onClick={() => onSelect('Project')}>
            <div className='paso-source-name'>Desde Otro Proyecto</div>
          </div>
        </Grid.Column>
      </Grid>
    </div>
  )
}

const parseTreeToItems = (tree: any[]) => {
  const retval = _.map(tree, (fase): HojaTrabajoTreeItem => {
    return {
      id: fase.id,
      descripcion: fase.descripcion,
      type: 'phase',
      items: _.map(fase.Subfases, (subfase: any): HojaTrabajoTreeItem => {
        return {
          id: subfase.id,
          descripcion: subfase.descripcion,
          type: 'subphase',
          items: _.map(subfase.MasterDataPaso, (paso: any): HojaTrabajoTreeItem => {
            return {
              id: paso.id,
              descripcion: paso.descripcion,
              code: paso.code,
              type: 'paso'
            }
          })
        }
      })
    }
  })

  return retval
}

const extractPasosCodes = (tree: any[]) => {
  let codes: string[] = []
  _.each(tree, (fase): any => {
    _.each(fase.Subfases, (subfase: any) => {
      _.each(subfase.MasterDataPaso, (paso: any) => {
        codes.push(paso.code)
      })
    })
  })

  return codes
}

const getQueryUrlParams = (): any => {
  let queryString = location.search.slice(1),
    params: any = {}

  queryString.replace(/([^=]*)=([^&]*)&*/g, function(_, key, value) {
    params[key] = value
    return _
  })

  return params
}

const setQueryUrlParams = (params: any) => {
  var query = [],
    key,
    value

  for (key in params) {
    if (!params.hasOwnProperty(key)) continue
    value = params[key]
    query.push(key + '=' + value)
  }

  location.search = query.join('&')
}

const updateQueryUrlParams = (param: string, value: string) => {
  var params = getQueryUrlParams()

  if (params[param] != value) {
    params[param] = value
    setQueryUrlParams(params)
  }
}

const USED_PASO_CODES = extractPasosCodes(app.vars.plan_auditoria.fases)

type HojaTrabajoTreeItem = {
  id: string
  descripcion: string
  type: string
  code?: string
  items?: HojaTrabajoTreeItem[]
}

type TreeItemFaseProps = {
  item: HojaTrabajoTreeItem
  onChecked: (item: HojaTrabajoTreeItem, isChecked: boolean) => void
}

const TreeItemFase = ({ item, onChecked }: TreeItemFaseProps) => {
  const [ collapsed, setCollapsed ] = useState(true)

  const onClick = (ev: SyntheticEvent) => {
    ev.preventDefault()
    setCollapsed(!collapsed)
  }

  const countPending = (item: HojaTrabajoTreeItem) => {
    let hasPending = false

    if (item.items) {
      _.each(item.items, function(subitem) {
        hasPending = hasPending || countPending(subitem)
        if (hasPending) {
          return true
        }
      })
    } else if (item.code && !_.contains(USED_PASO_CODES, item.code)) {
      hasPending = true
    }

    return hasPending
  }

  const hasPending = countPending(item)

  return (
    <li key={item.id} className={cx('methodology-item', item.type)}>
      <div className='methodology-item-head'>
        <a href='#' onClick={onClick}>
          <span className='item-name'>{item.descripcion}</span>
        </a>
        {hasPending && (
          <Popup
            trigger={
              <span className='pull-right ui-state-error-text'>
                <span className='icon-info-sign' />
              </span>
            }
            content='Este elemento contiene pasos que no se encuentran en su Plan de Auditora'
            on='hover'
            className='disabled-text'
          />
        )}
      </div>
      <ul className={cx('methodology collapse', collapsed ? 'hide' : 'in')}>
        {_.map(item.items as any[], (subitem) => {
          subitem.parent = item
          return subitem.type == 'paso' ? (
            <TreeItemPaso
              key={subitem.id}
              item={subitem}
              onChecked={(checked: boolean) => onChecked(subitem, checked)}
            />
          ) : (
            <TreeItemFase key={subitem.id} item={subitem} onChecked={onChecked} />
          )
        })}
      </ul>
    </li>
  )
}

const TreeItemPaso = ({ item, onChecked }: { item: HojaTrabajoTreeItem; onChecked: (checked: boolean) => void }) => {
  let [ isChecked, setChecked ] = useState(false)
  const used_coded = _.contains(USED_PASO_CODES, item.code)
  const onChangeCheckbox = (ev: SyntheticEvent) => {
    setChecked((ev.target as any).checked)
    onChecked((ev.target as any).checked)
  }

  const itemClasses = cx(
    'methodology-item paso',
    used_coded && !isChecked ? 'ui-state-disabled' : null,
    isChecked ? 'selected' : null
  )

  return (
    <li key={item.id} className={itemClasses}>
      <div className='methodology-item-head'>
        <label htmlFor={`tree-paso-${item.id}`}>
          <input
            type='checkbox'
            id={`tree-paso-${item.id}`}
            className='checkbox'
            checked={isChecked}
            onChange={onChangeCheckbox}
          />
          <span className='item-name'>
            {item.descripcion}
            {used_coded && (
              <span className='label label-important' style={{ marginLeft: 5 }}>
                Agregado
              </span>
            )}
          </span>
        </label>
      </div>
    </li>
  )
}

type ModalStep2MaterDataProps = {
  onSelect: (values: string[]) => void
  rubroId: string
}

type ModalStep2MaterDataState = {
  isLoading: boolean
  selectedPasos: string[]
}

class ModalStep2MaterData extends Component<ModalStep2MaterDataProps, ModalStep2MaterDataState> {
  static masterData: any = null

  state: ModalStep2MaterDataState = {
    isLoading: false,
    selectedPasos: []
  }

  componentDidMount() {
    this._loadMasterData()
  }

  _loadMasterData() {
    if (ModalStep2MaterData.masterData == null) {
      this.setState({ isLoading: true })
      $.getJSON(url_for(`proyecto/${app.vars.plan_auditoria.proyecto.id}/master_data_tree`)).done((res) => {
        ModalStep2MaterData.masterData = parseTreeToItems(res.response)
        this.setState({ isLoading: false })
      })
    }
  }

  onChangeCheckedPaso = (item: HojaTrabajoTreeItem, isChecked: boolean) => {
    let { selectedPasos } = this.state
    if (isChecked) {
      if (!(item.id in selectedPasos)) {
        selectedPasos.push(item.id)
      }
    } else {
      selectedPasos = _.reject(selectedPasos, function(element) {
        return element == item.id
      })
    }

    this.setState({ selectedPasos })
    this.props.onSelect(selectedPasos)
  }

  onSubmit = () => {
    $.blockUI(create_blockUI_loading_message('Guardando cambios...'))

    let postParams = {
      item_type: 'paso',
      parent_id: this.props.rubroId,
      items: this.state.selectedPasos
    }
    $.post(url_for(`proyecto/${app.vars.plan_auditoria.proyecto.id}/copy_from`), postParams)
      .done((res) => {
        if (!res.hasErrors) {
          updateQueryUrlParams('rubro', this.props.rubroId)
          location.reload()
        }
      })
      .fail(() => {
        $.unblockUI()
      })
  }

  renderTree() {
    return (
      <ul className='methodology checkbox-list methodology-all nav nav-stacked'>
        {_.map(ModalStep2MaterData.masterData, (fase: HojaTrabajoTreeItem) => {
          return (
            <TreeItemFase key={`tree-item-${fase.type}-${fase.id}`} item={fase} onChecked={this.onChangeCheckedPaso} />
          )
        })}
      </ul>
    )
  }

  render() {
    return (
      <div className='step-content step-content-2 step-content-2-masterdata'>
        <h4 className='ui centered header'>Seleccione los pasos que desea copiar a su Plan de Auditora?</h4>
        {this.state.isLoading ? (
          <div className='ui segment' style={{ minHeight: 200 }}>
            <Dimmer active inverted>
              <Loader inverted content='Cargando datos...' />
            </Dimmer>
          </div>
        ) : (
          <div>
            {this.renderTree()}
            <div className='btn-actions'>
              <Popup
                trigger={
                  <button
                    onClick={this.onSubmit}
                    className='btn btn-primary pull-right'
                    disabled={this.state.selectedPasos.length == 0}
                  >
                    Guardar
                  </button>
                }
                content='Debe seleccionar por lo menos un paso a copiar'
                header='Guardar | Opcin deshabilitada'
                disabled={this.state.selectedPasos.length > 0}
                className='disable-text'
                on='hover'
              />
            </div>
            <div className='clearfix' />
          </div>
        )}
      </div>
    )
  }
}

type ModalPasoCopyFromProps = {
  rubro: ItemSubfase
}

type ModalPasoCopyFromState = {
  isOpen: boolean
  step: number
  sourceSelected?: 'MasterData' | 'Project'
  pasosSelected: string[]
}

export class ModalPasoCopyFrom extends Component<ModalPasoCopyFromProps, ModalPasoCopyFromState> {
  state: ModalPasoCopyFromState = {
    isOpen: false,
    step: 2,
    sourceSelected: 'MasterData',
    pasosSelected: []
  }

  onClose = () => {
    this.setState({ isOpen: false })
  }

  onSelectSource = (sourceSelected: 'MasterData' | 'Project') => {
    this.setState({ step: 2, sourceSelected })
  }

  onSelectPasos = (pasosSelected: string[]) => {
    this.setState({ pasosSelected })
  }

  getStepContent() {
    switch (this.state.step) {
      case 1:
        return <ModalStep1 onSelect={this.onSelectSource} />
      case 2:
        switch (this.state.sourceSelected) {
          case 'MasterData':
            return <ModalStep2MaterData rubroId={this.props.rubro.id} onSelect={this.onSelectPasos} />
        }
    }
  }

  renderModal() {
    const { rubro } = this.props

    let sourceSelected = ''
    switch (this.state.sourceSelected) {
      case 'MasterData':
        sourceSelected = 'Biblioteca de Pasos'
        break
    }

    return (
      <Modal open className='modal-ab modal-pasocopyfrom' onClose={this.onClose} closeIcon closeOnDimmerClick={false}>
        <Modal.Header content={`Copiar paso desde... ${sourceSelected}`} />
        <Modal.Content>
          <p>
            <strong>Rubro:</strong> {rubro.descripcion}
          </p>

          {this.getStepContent()}
        </Modal.Content>
      </Modal>
    )
  }

  render() {
    if (app.vars.plan_auditoria.proyecto.version == '1') {
      return (
        <Popup
          header='Opcin deshabilitada'
          content='Esta opcin no esta disponible para esta versin de proyectos'
          trigger={<a className='disabled ui-state-disabled'>Copiar Paso desde Biblioteca</a>}
          position='left center'
          size='small'
          className='disabled-text'
        />
      )
    } else {
      return (
        <Fragment>
          <a
            href='#'
            onClick={(ev) => {
              ev.preventDefault()
              this.setState({ isOpen: true })
            }}
          >
            Copiar Paso desde Biblioteca
          </a>

          {this.state.isOpen && this.renderModal()}
        </Fragment>
      )
    }
  }
}
