/// <reference path="./AppTree.d.ts" />

import { PureComponent, Fragment } from 'react'
import PlanAuditoriaContext from './PlanAuditoriaContext'

import { ModalPasoNew, ModalPasoEdit, ModalPasoAssoc } from './ModalPasoNew'
import { ModalPasoCopyFrom } from './ModalPasoCopyFrom'
import Dropdown from 'semantic-ui-react/dist/commonjs/modules/Dropdown/Dropdown'
import Modal from 'semantic-ui-react/dist/commonjs/modules/Modal/Modal'

declare var app: any

export class TreeItemFaseActions extends PureComponent<{ item: ItemFase }> {
  static contextType = PlanAuditoriaContext

  onClickEdit = (ev: React.MouseEvent) => {
    ev.preventDefault()

    const { item } = this.props
    const { tree, updateTree } = this.context

    const prompt = bootbox.prompt(
      {
        header: `Renombrar Fase`,
        content: '<div>Escriba el nuevo nombre que desea asignarle a la fase seleccionada.</div>'
      },
      'Cancelar',
      'Actualizar',
      (r: string) => {
        if (!r || _.str.trim(r) == '') {
          return
        }

        $.blockUI(create_blockUI_loading_message('Actualizando Fase...'))
        $.post(url_for(`proyecto/${app.vars.idproyecto}/master_data/fase/${item.id}`), { fase_name: r })
          .done((res) => {
            if (!res.hasErrors) {
              tree[item.index].descripcion = r

              updateTree(tree)
              prompt.modal('hide')

              $.unblockUI()
            } else {
              $.unblockUI()
              bootbox.alert(
                `<p>El siguiente ocurri al intentar actualizar la fase:</p>
                <div class="alert alert-error alert-block _a-center">${res.message}</div>`
              )
            }
          })
          .fail(() => {
            $.pnotify({ type: 'error', text: 'Un error inesperado ocurri al intentar actualizar la fase.' })
            $.unblockUI()
          })

        return false
      },
      item.descripcion
    )
    $('.bootbox.modal').addClass('bootbox-prompt modal-ab')
  }

  onClickDelete = (ev: React.MouseEvent) => {
    ev.preventDefault()

    const { item } = this.props

    if (item.id == app.vars.plan_auditoria.settings.ejecucion_fase_id) {
      bootbox.alert(
        'Esta fase NO es posible eliminarla, debido a que ha sido marcada como la fase de ejecucin de este proyecto.'
      )
      return
    }

    let { tree, updateTree } = this.context

    bootbox.warningConfirm(
      `Esta seguro de querer eliminar por completo la fase "<strong>${item.descripcion}</strong>"?`,
      'No',
      'S, Eliminar fase',
      (answer) => {
        if (!answer) {
          return
        }

        $.blockUI(create_blockUI_loading_message('Eliminando Fase...'))
        $.post(url_for(`proyecto/${app.vars.idproyecto}/master_data/fase/${item.id}`), { sf_method: 'delete' })
          .done(() => {
            tree = _.reject(tree, { id: item.id })
            updateTree(tree)
            $.unblockUI()
          })
          .fail(() => $.unblockUI())
      }
    )
  }

  onClickAdd = (ev: React.MouseEvent) => {
    ev.preventDefault()

    const { item } = this.props
    const { tree, updateTree } = this.context

    const prompt = bootbox.prompt(
      {
        header: `Crear Rubro`,
        content: `<blockquote><strong>Fase: </strong>${item.descripcion} </blockquote> <div>Escriba el nuevo nombre del rubro a crear.</div>`
      },
      'Cancelar',
      'Crear',
      (r: string) => {
        if (!r || _.str.trim(r) == '') {
          return
        }

        $.blockUI(create_blockUI_loading_message('Creando Rubro...'))
        $.post(url_for(`proyecto/${app.vars.idproyecto}/master_data/fase`), {
          id_parent: item.id,
          fase_name: r
        })
          .done((res) => {
            if (!res.hasErrors) {
              const subfase = res.response.record

              tree[item.index].items.push({
                id: subfase.id_master_data_fase,
                code: subfase.code,
                descripcion: subfase.descripcion,
                position: subfase.position,
                index: tree[item.index].items.length,
                sortIndex: tree[item.index].items.length,
                open: false,
                sumarias: null,
                items: []
              })

              tree[item.index].open = true

              updateTree(tree)
              prompt.modal('hide')

              $.unblockUI()
            } else {
              $.unblockUI()
              bootbox.alert(
                `<p>El siguiente ocurri al intentar crear el rubro:</p>
                <div class="alert alert-error alert-block _a-center">${res.message}</div>`
              )
            }
          })
          .fail(() => {
            $.pnotify({ type: 'error', text: 'Un error inesperado ocurri al intentar crear el rubro.' })
            $.unblockUI()
          })

        return false
      },
      ''
    )
    $('.bootbox.modal').addClass('bootbox-prompt modal-ab')
  }

  render() {
    return (
      <div className='pull-right sf_admin_td_actions'>
        <div className='btn-group dropdown-modern'>
          <a className='btn btn-mini dropdown-toggle' data-toggle='dropdown' href='#'>
            <span className='icon-tc-ellipsis-v' />
          </a>

          <ul className='dropdown-menu pull-right'>
            {user_has_role_permission('fase_edit') && (
              <li>
                <a href='#' onClick={this.onClickEdit}>
                  Editar
                </a>
              </li>
            )}
            {user_has_role_permission('subfase_create') && (
              <li>
                <a href='#' onClick={this.onClickAdd}>
                  Crear Rubro
                </a>
              </li>
            )}
            {user_has_role_permission('fase_delete') && (
              <Fragment>
                <li className='divider' />
                <li>
                  <a href='#' onClick={this.onClickDelete}>
                    Eliminar
                  </a>
                </li>
              </Fragment>
            )}
          </ul>
        </div>
      </div>
    )
  }
}

