export class TreeItemPasoActions extends PureComponent<{ item: ItemPaso }> {
  static contextType = PlanAuditoriaContext

  onClickDelete = (ev: React.MouseEvent) => {
    ev.preventDefault()

    const { item } = this.props
    let { tree, updateTree } = this.context

    bootbox.warningConfirm(
      `Esta seguro de querer eliminar por completo el paso "<strong>${item.descripcion}</strong>"?`,
      'No',
      'S, Eliminar paso',
      (answer) => {
        if (!answer) {
          return
        }

        $.blockUI(create_blockUI_loading_message('Eliminando Paso...'))
        $.post(url_for(`proyecto/${app.vars.idproyecto}/master_data/paso/${item.id}`), { sf_method: 'delete' })
          .done(() => {
            tree[item.parent.parent.index].items[item.parent.index].items = _.reject(
              tree[item.parent.parent.index].items[item.parent.index].items,
              { id: item.id }
            )
            updateTree(tree)
            $.unblockUI()
          })
          .fail(() => $.unblockUI())
      }
    )
  }

  render() {
    const { item } = this.props
    return (
      <div className='pull-right sf_admin_td_actions'>
        <div className='btn-group dropdown-modern'>
          <a className='btn btn-mini dropdown-toggle' data-toggle='dropdown' href='#'>
            <span className='icon-tc-ellipsis-v' />
          </a>

          <ul className='dropdown-menu pull-right'>
            {user_has_role_permission('paso_edit') && (
              <Fragment>
                <li>
                  <ModalPasoEdit paso={item} />
                </li>
                {!_.include([ 'PERIODO', 'MATERIALIDAD' ], item.tipo) && (
                  <li>
                    <ModalPasoAssoc paso={item} />
                  </li>
                )}
              </Fragment>
            )}
            {!_.include([ '14', '2A9' ], item.code) &&
            user_has_role_permission('paso_delete') && (
              <Fragment>
                <li className='divider' />
                <li>
                  <a href='#' onClick={this.onClickDelete}>
                    Eliminar
                  </a>
                </li>
              </Fragment>
            )}
          </ul>
        </div>
      </div>
    )
  }
}

export class DropdownItemAssignUser extends PureComponent<
  { paso_visita: any; paso: ItemPaso },
  { isOpen: boolean; id_asignado: string }
> {
  static contextType = PlanAuditoriaContext

  constructor(props: { paso_visita: any; paso: ItemPaso }) {
    super(props)

    this.state = {
      isOpen: false,
      id_asignado: props.paso_visita.id_usuario_asignado
    }
  }

  onChange = (ev: React.SyntheticEvent, data: any) => {
    this.setState({ id_asignado: data.value })
  }

  onSubmit = () => {
    const { paso, paso_visita } = this.props
    const { tree, updateTree } = this.context

    const paso_visita_id = paso_visita.id_master_data_paso_has_proyecto_visita

    $.blockUI(create_blockUI_loading_message('Guardando cambios'))
    $.post(url_for(`proyecto/${app.vars.idproyecto}/master_data/visita/${paso_visita_id}/asignar`), {
      paso_id_usuario: this.state.id_asignado
    })
      .done((res) => {
        if (!res.hasErrors) {
          tree[paso.parent.parent.index].items[paso.parent.index].open = true
          tree[paso.parent.parent.index].items[paso.parent.index].items[paso.index].paso_visitas =
            res.response.paso_visitas

          updateTree(tree)
        }
        $.unblockUI()
      })
      .fail(() => $.unblockUI())

    this.setState({ isOpen: false })
  }

  onClickItem = () => {
    this.setState({ isOpen: true })
    setTimeout(() => $('.ui.dropdown.contextmenu .menu.visible').removeClass('visible'), 30)
  }

  render() {
    const { paso, paso_visita } = this.props
    const { visitas } = this.context
    const visita: ProyectoVisita = _.find(visitas, { id_proyecto_visita: paso_visita.id_proyecto_visita })

    const equipoOptions: any = _.map(app.vars.plan_auditoria.equipo, function(item: ProyectoEquipo) {
      return {
        key: item.user_id,
        text: item.name,
        value: item.user_id,
        description: item.rol.name,
        label: { content: item.sigla, circular: true, color: 'green' }
      }
    })

    return (
      <Fragment>
        <Dropdown.Item onClick={this.onClickItem}>Asignar usuario</Dropdown.Item>
        {this.state.isOpen && (
          <Modal
            open
            size='tiny'
            dimmer='inverted'
            className='modal-ab modal-pasouser'
            onClose={() => this.setState({ isOpen: false })}
            closeIcon
          >
            <Modal.Header content='Asignar usuario' />
            <Modal.Content>
              <blockquote>
                <div>
                  <strong>Rubro:</strong> {paso.parent.descripcion}
                </div>
                <div>
                  <strong>Paso:</strong> {paso.descripcion}
                </div>
                {paso.tipo == 'PERIODO' ? (
                  <div>
                    <strong>Periodo:</strong> {paso_visita.visita_alias}
                  </div>
                ) : (
                  <div>
                    <strong>Visita:</strong> {visita.name}
                  </div>
                )}
              </blockquote>

              <div className='ui form'>
                <div className='field'>
                  <label htmlFor=''>Asocie las visitas relacionadas con este paso</label>
                  <br />
                  <Dropdown
                    placeholder='Seleccione el usuario'
                    defaultValue={paso_visita.id_usuario_asignado}
                    selection
                    clearable
                    fluid
                    options={equipoOptions}
                    onChange={this.onChange}
                  />
                </div>
              </div>
            </Modal.Content>
            <Modal.Actions>
              <button className='ui button negative' onClick={() => this.setState({ isOpen: false })}>
                Cancelar
              </button>
              <button className='ui button primary' onClick={this.onSubmit}>
                Aceptar
              </button>
            </Modal.Actions>
          </Modal>
        )}
      </Fragment>
    )
  }
}

export class DropdownItemDeletePasoVisita extends PureComponent<{ paso_visita: any; paso: ItemPaso }> {
  static contextType = PlanAuditoriaContext

  onClickDelete = () => {
    const { paso, paso_visita } = this.props
    const { tree, updateTree, visitas } = this.context

    const visita: ProyectoVisita = _.find(visitas, { id_proyecto_visita: paso_visita.id_proyecto_visita })

    bootbox.warningConfirm(
      `Est seguro de querer desvincular la visita <strong>"${visita.name}"</strong> del paso <strong>"${paso.descripcion}"</strong>?`,
      'No',
      'S, Desvincular visita',
      (answer) => {
        if (!answer) {
          return
        }

        const paso_visita_id = paso_visita.id_master_data_paso_has_proyecto_visita
        $.blockUI(create_blockUI_loading_message('Desvinculando Visita del Paso...'))
        $.post(url_for(`proyecto/${app.vars.idproyecto}/master_data/visita/${paso_visita_id}/desasociar`), {
          sf_method: 'delete'
        })
          .done((res) => {
            tree[paso.parent.parent.index].items[paso.parent.index].open = true
            tree[paso.parent.parent.index].items[paso.parent.index].items[paso.index].paso_visitas =
              res.response.paso_visitas

            updateTree(tree)
            $.unblockUI()
          })
          .fail(() => $.unblockUI())
      }
    )
  }

  render() {
    return (
      <Dropdown.Item key='delete' onClick={this.onClickDelete}>
        Desvincular visita
      </Dropdown.Item>
    )
  }
}
